import { openSidePanel, settings, sidePanelOpened } from "./runtime";

export function showFloatBall() {
    const button = document.getElementById("sidepanel-ball-container");
    if (!button) {
        const container = document.createElement('div');
        container.id = 'sidepanel-ball-container';
        container.style.position = 'fixed';
        container.style.top = '30%';
        container.style.right = '0px';
        container.style.zIndex = '2147483647';

        const fab = document.createElement('div');
        fab.id = 'fab';
        fab.style.width = '32px';
        fab.style.height = '32px';
        fab.style.backgroundColor = '#ff4081';
        fab.style.borderRadius = '32px 0 0 32px';
        fab.style.display = 'flex';
        fab.style.alignItems = 'center';
        fab.style.justifyContent = 'flex-start';
        fab.style.boxShadow = '0px 3px 5px rgba(0,0,0,0.3)';
        fab.style.cursor = 'pointer';
        fab.style.transition = 'transform 0.3s';
        fab.style.opacity = '0.1';

        const fabIcon = document.createElement('div');
        fabIcon.id = 'fab-icon';
        fabIcon.style.width = '24px';
        fabIcon.style.height = '24px';
        fabIcon.style.display = 'flex';
        fabIcon.style.alignItems = 'center';
        fabIcon.style.justifyContent = 'center';
        fabIcon.style.margin = '0 8px';

        fabIcon.innerHTML = `<svg xmlns="http://www.w3.org/2000/svg" height="20px" viewBox="0 -960 960 960" width="20px" fill="#FFF"><path d="M160-160q-33 0-56.5-23.5T80-240v-480q0-33 23.5-56.5T160-800h640q33 0 56.5 23.5T880-720v480q0 33-23.5 56.5T800-160H160Zm540-453h100v-107H700v107Zm0 186h100v-106H700v106ZM160-240h460v-480H160v480Zm540 0h100v-107H700v107Z"/></svg>`;
        fab.appendChild(fabIcon);
        container.appendChild(fab);

        document.body.appendChild(container);



        const showFab = () => {
            fab.style.width = '54px';
            fab.style.transition = 'width 0.2s';
            setTimeout(() => {
                fab.style.opacity = '1';
            }, 300);
        };

        const hideFab = () => {
            fab.style.width = '32px';
            fab.style.transition = 'width 0.2s';
            fab.style.opacity = '0.3';
        };

        setTimeout(() => {
            hideFab();
        }, 1500);
        fab.addEventListener('mouseenter', showFab);
        fab.addEventListener('mouseleave', hideFab);
        fab.addEventListener('click', async () => {
            fab.style.transform = 'scale(1)';
            openSidePanel();
            container.remove();
        });



        // Load saved position
        const savedTopPercent = localStorage.getItem('sidepanelBallTopPercent');
        if (savedTopPercent !== null) {
            container.style.top = savedTopPercent;
        }

        let startY, offsetY;

        fab.addEventListener('mousedown', (e) => {
            startY = e.clientY;
            offsetY = container.offsetTop;
            document.addEventListener('mousemove', onMouseMove);
            document.addEventListener('mouseup', onMouseUp);
        });

        function onMouseMove(e) {
            const newY = offsetY + e.clientY - startY;
            const windowHeight = window.innerHeight;

            // Ensure the element doesn't go out of the window bounds
            const topBoundary = 0;
            const bottomBoundary = windowHeight - container.clientHeight;

            if (newY >= topBoundary && newY <= bottomBoundary) {
                container.style.top = newY + 'px';
            }
        }

        function onMouseUp() {
            // Save the position as a percentage
            const topPercent = (container.offsetTop / window.innerHeight) * 100 + '%';
            localStorage.setItem('sidepanelBallTopPercent', topPercent);
            document.removeEventListener('mousemove', onMouseMove);
            document.removeEventListener('mouseup', onMouseUp);
        }

        document.addEventListener('mouseup', () => {
            document.removeEventListener('mousemove', onMouseMove);
            document.removeEventListener('mouseup', onMouseUp);
        });


        function highlightFab() {
            if (!document.fullscreenElement) {
                //exited fullscreen
                setTimeout(() => {
                    showFab();
                    setTimeout(() => {
                        hideFab();
                    }, 2000);
                }, 1000);
            }
        }
        document.removeEventListener('fullscreenchange', highlightFab);
        document.addEventListener('fullscreenchange', highlightFab);
    }
}


export function removeFloatBall() {
    const button = document.getElementById("sidepanel-ball-container");
    if (button) {
        button.remove();
    }
}

export const initFloatBall = () => {
    if (settings?.floatBall) {
        if (sidePanelOpened) {
            removeFloatBall();
        } else {
            showFloatBall();
        }
    } else {
        removeFloatBall();
    }
}
