import { activeVideoEnterPictureInPicture, exitPictureInPicture } from "./pictureInPicture";
import { clearAllData, getAllLocalStorageItems, getAllSessionStorageItems } from "./pageStorage";
import { sendRuntimeMessage } from "../../core/browers/runtime";
import { initFloatBall, removeFloatBall, showFloatBall } from "./floatBall";
import { pageScrolledObserver, scrollToBottom, scrollToTop } from "./pageScroll";
import { delay } from "lodash-es";
import { handleTabChanged } from "./tabs";
import { updatePageBySiteConfigs } from "./updatePage";
import { createSidebar, removeSidebar } from "./sidebar";
import { enterDocumentPip, exitDocumentPip, initDocumentPip, playingVideoEnterDocumentPip } from "./documentPictureInPicture";
import { pauseVideo, playbackRateChange, playVideo, seekVideo, switchVideoMute, volumeVideo } from "./video";

export let settings = {};
export let siteConfigs = {};
export let tab = {};
export let sidePanelOpened = false;
export let frameId = null;
export let sidePanelPorts = new Map();

export const startup = async () => {
    await initSettings();
    delay(pageScrolledObserver, 1000);
};

export const initSettings = async () => {
    const response = await sendRuntimeMessage("get_settings");
    console.log("initSettings", response);
    if (response?.success && response.data?.settings) {
        ({ settings, siteConfigs, tab, frameId } = response.data);
        if (siteConfigs) {
            updatePageBySiteConfigs();
        }
    }
    const autoPip = response?.success && (response.data?.tabConfig?.enterPictureInPicture || siteConfigs?.autoEnterPictureInPicture);
    if (autoPip) {
        window.addEventListener('load', () => {
            console.log("page is fully loaded");
            setTimeout(() => {
                settings?.advancedPipEnable ? enterDocumentPip(true) : activeVideoEnterPictureInPicture();
            }, 500);
        });
    }

    const page_keep_alive = response?.success && (response.data?.tabConfig?.page_keep_alive || siteConfigs?.pageKeepAlive);
    if (page_keep_alive) {
        window.postMessage({ action: "page_keep_alive" }, location.origin);
    }

    const sidePanelResponse = await sendRuntimeMessage("is_sidepanel_opened");
    console.log("sidePanelResponse", sidePanelResponse);
    sidePanelOpened = sidePanelResponse?.success && sidePanelResponse.data.is_sidepanel_opened === true;

    if (sidePanelResponse?.data && frameId === 0) {
        if (settings?.floatBall) {
            sidePanelResponse.data.is_sidepanel_opened ? removeFloatBall() : showFloatBall();
        }
        if (settings?.sidebarEnabled) {
            sidePanelResponse.data.is_sidepanel_opened ? removeSidebar() : createSidebar();
        }
    }
    initDocumentPip();
};

export const processContentScriptsMessage = async (message, sender, sendResponse) => {
    try {
        switch (message.action) {
            case 'update_setting':
                settings[message.data.key] = message.data.value;
                if (message.data.key === "floatBall") {
                    initFloatBall();
                }
                if (["theme", "sidebarEnabled", "sidebarPosition"].includes(message.data.key)) {
                    removeSidebar();
                    createSidebar();
                }
                break;
            case 'update_site_config':
                siteConfigs[message.data.key] = message.data.value;
                updatePageBySiteConfigs();
                break;
            case 'tabUpdated':
                handleTabChanged(message.data.tabId, message.data.changeInfo, message.data.tab);
                break;
            case 'tab_url_updated':
                await startup();
                break;
            case 'save_tab_session':
                return {
                    localStorageItems: getAllLocalStorageItems(),
                    sessionStorageItems: getAllSessionStorageItems()
                };
            case 'clear_browsing_data':
                clearAllData(message.data?.options);
                break;
            case 'play_video':
                playVideo();
                break;
            case 'pause_video':
                pauseVideo();
                break;
            case 'video_jump_to_time':
                seekVideo(message.data.value);
                break;
            case 'volume_change':
                volumeVideo(message.data.value);
                break;
            case 'playback_rate_change':
                playbackRateChange(message.data.value);
                break;
            case 'video_switch_mute':
                switchVideoMute();
                break;
            case 'page_keep_alive':
                window.postMessage({ action: "page_keep_alive" }, location.origin);
                break;
            case 'page_keep_alive_cancel':
                window.postMessage({ action: "page_keep_alive_cancel" }, location.origin);
                break;
            case 'enterPictureInPicture':
            case 'playing_video_enterPictureInPicture':
                settings?.advancedPipEnable
                    ? (message.action === 'enterPictureInPicture' ? enterDocumentPip(true) : playingVideoEnterDocumentPip())
                    : activeVideoEnterPictureInPicture();
                break;
            case 'exitPictureInPicture':
            case 'playing_video_exitPictureInPicture':
                settings?.advancedPipEnable ? exitDocumentPip() : exitPictureInPicture();
                break;
            case 'openPopup':
                await sendRuntimeMessage("update_setting", { key: "clickExtensionIconAction", value: 'openPopup' });
                break;
            case 'sidepanel_opened':
                if (message.data?.name === `sidepanel_${tab?.windowId}`) {
                    removeFloatBall();
                    removeSidebar();
                    sidePanelOpened = true;
                }
                if (!settings) {
                    await startup();
                }
                break;
            case 'sidepanel_closed':
                sidePanelOpened = false;
                if (message.data?.name === `sidepanel_${tab?.windowId}` && frameId === 0) {
                    if (settings?.floatBall) showFloatBall();
                    if (settings?.sidebarEnabled) createSidebar();
                }
                break;
            case 'scroll_to_top':
                scrollToTop();
                break;
            case 'scroll_to_bottom':
                scrollToBottom();
                break;
            case 'redirect_url':
                window.location.href = message.data.url;
                break;
            case 'update_tab_title':
                document.title = message.data.title
                break;
            default:
                throw new Error('Unknown action');
        }
        sendResponse({ success: true });
    } catch (e) {
        console.error("runtime got error", { err: e, errmsg: e.message });
        sendResponse({ success: false, error: e.message });
    }
};

export const openSidePanel = async () => {
    await sendRuntimeMessage("open_sidepanel");
};

const sidePanelEvents = ["keydown", "mousedown", "pointerdown", "pointerup", "touchend"];

export const registerOpenSidePanelEvents = () => {
    sidePanelEvents.forEach(event => document.addEventListener(event, openSidePanel, { once: true }));
};

export const unregisterOpenSidePanelEvents = () => {
    sidePanelEvents.forEach(event => document.removeEventListener(event, openSidePanel, { once: true }));
};