import { fromUrl, parseDomain } from "parse-domain";
import toString from 'lodash-es/toString';
import orderBy from 'lodash-es/orderBy';

export function getDomainFromUrl(url) {
  if (!url || !isValidUrl(url)) return url ? toString(url).substring(0, 20) : ""
  let domaininfo = parseDomain(fromUrl(url));
  return domaininfo.domain + "." + domaininfo.topLevelDomains?.join(".")
}
export function getHostnameFromUrl(url) {
  try {
    return new URL(url).hostname
  } catch (e) {
    console.error(`getHostnameFromUrl ${url} error`, e);
    return url ? toString(url).substring(0, 20) : ""
  }
}

export const isValidUrl = function (string) {
  const regex = /^(http:\/\/|https:\/\/).*$/;
  return regex.test(string);
}


export function getChromeVersion() {
  var raw = navigator.userAgent.match(/Chrom(e|ium)\/([0-9]+)\./);
  return raw ? parseInt(raw[2], 10) : false;
}

export const replaceAndAddObjectsByKey = (arrayA, arrayB, key = 'name') => {
  const keysInA = new Set(arrayA.map(obj => obj[key]));

  const updatedArrayA = arrayA.map(objA => {
    const objB = arrayB.find(objB => objB[key] === objA[key]);
    return objB || objA;
  });

  //  arrayB arrayA  arrayA 
  arrayB.forEach(objB => {
    if (!keysInA.has(objB[key])) {
      updatedArrayA.push(objB);
    }
  });
  return updatedArrayA;
}

export const asyncSleep = (ms) => {
  return new Promise(resolve => setTimeout(resolve, ms));
}

export function copyTextToClipboard(text) {
  if (navigator.clipboard) {
    return navigator.clipboard.writeText(text).then(() => true, () => false);
  }
  const textArea = document.createElement("textarea");
  textArea.value = text;
  textArea.style.position = "fixed";
  textArea.style.opacity = "0";
  document.body.appendChild(textArea);
  textArea.focus();
  textArea.select();
  let successful = false;
  try {
    successful = document.execCommand("copy");
  } catch (err) {
    console.error(`copyTextToClipboard error`, err);
    successful = false;
  }
  document.body.removeChild(textArea);
  return successful;
}



export function hexToRgb(hex, alpha = 1) {
  hex = hex.replace("#", "").trim()
  let r = parseInt(hex.substr(0, 2), 16);
  let g = parseInt(hex.substr(2, 2), 16);
  let b = parseInt(hex.substr(4, 2), 16);
  return `rgb(${r}, ${g}, ${b},${alpha})`;
}

export const filterTabs = (tabs, searchText, orderByParam, sortBy, activeWindowId) => {
  if (activeWindowId) {
    tabs = tabs.map(tab => { return { ...tab, inActiveWindow: tab.windowId === activeWindowId ? 1 : 0 } });
    tabs = orderBy(tabs, ["inActiveWindow", sortBy], ['desc', orderByParam]);
  } else {
    if (orderByParam && sortBy) {
      tabs = orderBy(tabs, [sortBy], [orderByParam]);
    }
  }

  if (searchText.trim().length > 0) {
    const keywords = searchText.trim().toLowerCase().split(" ").filter(Boolean);
    tabs = tabs.filter(tab =>
      keywords.every(kw =>
        (tab.url && tab.url.toLowerCase().includes(kw)) ||
        (tab.title && tab.title.toLowerCase().includes(kw))
      )
    );
  }
  return tabs;
}

export function calculateClockAngle() {
  const now = new Date(); // 
  const hours = now.getHours() % 12; // 120-11
  const minutes = now.getMinutes(); // 

  // 
  const hourAngle = (hours + minutes / 60) * 30; // 300.5
  const minuteAngle = minutes * 6; // 6

  // 
  // // 180
  // angle = Math.min(angle, 360 - angle);

  return Math.abs(hourAngle - minuteAngle);
}




export const formatDuration = (duration) => {
  // Handle invalid input
  if (duration === null || duration === undefined) {
    return "00:00";
  }

  // Convert to number if possible
  const numDuration = Number(duration);

  // Handle NaN, negative, and Infinity cases
  if (isNaN(numDuration) || numDuration < 0) {
    return "00:00";
  }
  if (numDuration === Infinity || duration > 86400) {
    return '';
  }

  // Proceed with valid number
  const roundedDuration = Math.floor(numDuration);

  const hours = Math.floor(roundedDuration / 3600);
  const minutes = Math.floor((roundedDuration % 3600) / 60);
  const seconds = roundedDuration % 60;

  if (hours > 0) {
    return `${String(hours).padStart(2, '0')}:${String(minutes).padStart(2, '0')}:${String(seconds).padStart(2, '0')}`;
  } else {
    return `${String(minutes).padStart(2, '0')}:${String(seconds).padStart(2, '0')}`;
  }
}

export const formatDateYmdHis = (date) => {
  const targetDate = new Date(date);
  if (isNaN(targetDate)) {
    console.error("Invalid date");
    return "";
  }

  const Y = targetDate.getFullYear();
  const m = String(targetDate.getMonth() + 1).padStart(2, '0');
  const d = String(targetDate.getDate()).padStart(2, '0');
  const hh = String(targetDate.getHours()).padStart(2, '0');
  const mm = String(targetDate.getMinutes()).padStart(2, '0');
  const ss = String(targetDate.getSeconds()).padStart(2, '0');
  return `${Y}-${m}-${d} ${hh}:${mm}:${ss}`;
}

export function faviconURL(u, size = 32) {
  const url = new URL(chrome.runtime.getURL("/_favicon/"));
  url.searchParams.set("pageUrl", u);
  url.searchParams.set("size", size);
  return url.toString();
}

export async function fetchWebsiteIcon(url) {
  try {
    const response = await fetch(url);
    const text = await response.text();
    const parser = new DOMParser();
    const doc = parser.parseFromString(text, 'text/html');
    const iconLink = doc.querySelector("link[rel~='icon']");
    if (iconLink) {
      return iconLink.href;
    } else {
      throw new Error("Icon not found");
    }
  } catch (error) {
    console.error("Error fetching website icon:", error);
    return "";
  }
}


export const scrollToActiveTab = () => {
  const activeTabElement = document.querySelector('.tab-item.active');
  if (activeTabElement) {
    activeTabElement.scrollIntoView({ behavior: "instant", block: "center", inline: "nearest" });
  }
}


export const decodedUrl = (url, full = true) => {
  try {
    let newUrl = decodeURIComponent(url);
    if (!full) {
      const urlObj = new URL(newUrl);
      newUrl = urlObj.pathname + urlObj.search + urlObj.hash;
    }
    return newUrl;
  } catch (e) {
    console.error(`Invalid URL:${url}`, e);
    return url;
  }
}

export const getBrowser = () => {
  const userAgent = navigator.userAgent.toLowerCase();
  const browserInfo = navigator.userAgentData?.brands?.[0] || {};

  if (browserInfo.brand) {
    return browserInfo.brand;
  } else if (userAgent.includes("firefox")) {
    return "Firefox";
  } else if (userAgent.includes("edg")) {
    return "Edge";
  } else if (userAgent.includes("opr") || userAgent.includes("opera")) {
    return "Opera";
  } else if (userAgent.includes("chrome")) {
    return "Chrome";
  } else if (userAgent.includes("safari")) {
    return "Safari";
  } else if (userAgent.includes("trident") || userAgent.includes("msie")) {
    return "Internet Explorer";
  } else if (userAgent.includes("vivaldi")) {
    return "Vivaldi";
  } else if (userAgent.includes("brave")) {
    return "Brave";
  } else if (userAgent.includes("yandex")) {
    return "Yandex";
  } else if (userAgent.includes("seamonkey")) {
    return "SeaMonkey";
  } else if (userAgent.includes("chromium")) {
    return "Chromium";
  } else if (userAgent.includes("maxthon")) {
    return "Maxthon";
  } else if (userAgent.includes("ucbrowser")) {
    return "UC Browser";
  } else if (userAgent.includes("samsung")) {
    return "Samsung Internet";
  } else {
    return "Unknown";
  }
}

export const countBookmarkUrls = (bookmark) => {
  if (bookmark.url) {
    return 1;
  } else if (bookmark.children) {
    return bookmark.children.reduce((count, child) => count + countBookmarkUrls(child), 0);
  }
  return 0;
};

export function isUsableVideo(video) {

  const rect = video.getBoundingClientRect();

  // console.log("isUsableVideo", { rect, duration: video.duration, video: video })
  if (rect.width < 200 || rect.height < 200 || (rect.width > rect.height && rect.height / rect.width > 2.4)) {
    return false;
  }


  const isBottomRightQuarter = (rect.right >= window.innerWidth * 0.75) && (rect.bottom >= window.innerHeight * 0.75);
  const isSmallVideo = (rect.width * rect.height) <= (window.innerWidth * window.innerHeight * 0.25);
  if (isBottomRightQuarter && isSmallVideo) {
    return false;
  }

  if (video.duration >= 10 || video.duration === Infinity || isNaN(video.duration)) {
    return true;
  }

  return false;
}

export function calcSliderPos(e) {
  return (e.offsetX / e.target.clientWidth) * parseInt(e.target.getAttribute('max'), 10);
}

function isElementInViewport(element) {
  return new Promise(resolve => {
    const observer = new IntersectionObserver(([entry]) => {
      resolve(entry.isIntersecting);
      observer.disconnect();
    });
    observer.observe(element);
  });
}


export const addEventListenersOnce = (element, event, handler) => {
  element.removeEventListener(event, handler);
  element.addEventListener(event, handler);
};

let elementPositions = new Map()

export function saveElementPosition(element) {
  const parent = element.parentElement;
  const index = Array.from(parent.children).indexOf(element);
  elementPositions.set(element, { parent, index });
}

export function restoreElementPosition(element) {
  const position = elementPositions.get(element);
  if (position) {
    const { parent, index } = position;
    const children = Array.from(parent.children);
    if (index >= children.length) {
      parent.appendChild(element);
    } else {
      parent.insertBefore(element, children[index]);
    }
    console.log('Element restored to original position:', element, position);
  } else {
    console.error('No saved position for element:', element);
  }
}