import { playSvg, pauseSvg, fitToScreenSvg, muteSvg, prevSvg, nextSvg, unmuteSvg, captionSvg, danmakuSvg, soundDisabledSvg, playCircleSvg, keyboardSvg } from "../component/svgs";
import { activeVideoEnterPictureInPicture, exitPictureInPicture } from "./pictureInPicture";
import { sendRuntimeMessage } from "../../core/browers/runtime";
import { addEventListenersOnce, calcSliderPos, formatDuration, isUsableVideo, restoreElementPosition, saveElementPosition } from "../../utils/helpers";
import { settings, siteConfigs } from "./runtime";
import { debonceSendVideoProgressInfo, getDisneyVideoDuration, getMainVideo, isDisneyplus, isSupportDocumentPictureInPicture, pauseVideo, playVideo, seekVideo, sendPictureInPictureInfo, switchVideoMute, volumeVideo } from "./video";
import { delay, isNumber } from "lodash-es";
import { VideoTracker } from "./VideoTracker";

window.addEventListener('DOMContentLoaded', () => {
    mainWindow = window;
});

let tracker,
    pipWindow,
    mainWindow = window,
    videoContainer = null,
    originalVideoParent = null,
    isSeeking = false,
    isCloseing = false,
    isWaiting = false,
    trigger = null,
    config = null;

let danmakuInstance, captionInstance;


export const initDocumentPip = () => {
    if (!tracker) {
        tracker = new VideoTracker();
        tracker.on('initialized', async () => {
            console.log('VideoTracker has been initialized');
            sendPictureInPictureInfo();
            if (tracker.currentMainVideo) {
                await getConfig();
                window.postMessage({ action: "disable_page_hide_event" }, location.origin);
            }
        });

        tracker.init();

        tracker.on('mainVideoChanged', async () => {
            sendPictureInPictureInfo();
            if (tracker.currentMainVideo) {
                await getConfig();
                window.postMessage({ action: "disable_page_hide_event" }, location.origin);
            }
            if (tracker.currentMainVideo && documentPictureInPicture.window && !isCloseing) {
                isWaiting = false;
                setTimeout(async () => {
                    await createPlayer();
                }, 500);
            }
        });
        tracker.on('mainVideoPlay', () => {
            setTimeout(async () => {
                if (tracker.currentMainVideo && documentPictureInPicture.window && !isCloseing) {
                    isWaiting = false;
                    await createPlayer();
                }
            }, 500)

        });
        tracker.on('mainVideoProgress', ({ video, percent, readyState }) => {
            if (readyState < 3 && documentPictureInPicture.window) {
                setLoading(true, `Loading ${percent.toFixed(2)}%`);
            }
            console.log('Main video progress event:', { percent, readyState });
            // Perform actions based on the main video progress
        });
        tracker.on('mainVideoUpdated', (eventName, video) => {
            // console.log('Main video updated', eventName, { duration: video.duration, currentTime: video.currentTime, paused: video.paused, height: video.videoHeight, width: video.videoWidth });
            // Perform actions based on the new main video
            debonceSendVideoProgressInfo(video);
        });


        tracker.on('mainVideoIntersecting', (video) => {
            sendRuntimeMessage('page_video_intersection_changed', { isIntersecting: true, duration: tracker?.currentMainVideo })
        });
        tracker.on('mainVideoNotIntersecting', (video) => {
            sendRuntimeMessage('page_video_intersection_changed', { isIntersecting: false, duration: tracker?.currentMainVideo })
        });


        document.addEventListener('fullscreenchange', (event) => {
            console.log("fullscreenchange", event);
            sendRuntimeMessage(document.fullscreenElement ? "page_enter_fullscreen" : 'page_exit_fullscreen');

        }, false);
        window.addEventListener('enterpictureinpicture', (event) => {
            sendPictureInPictureInfo()
        });
        window.addEventListener('leavepictureinpicture', (event) => {
            event.target.scrollIntoViewIfNeeded();
            sendPictureInPictureInfo();
        });




    }
    return tracker;
}
const initConfig = async () => {
    const date = new Date();
    config = {
        cssUrls: [],
        localCss: [`css/pip.css`],
        styles: ""
    };

    if (document.querySelector('.video-js')) {
        config.localCss.push(`css/vjs.css`);
    }

    const hostname = window.location.hostname;
    const sessionKey = `vertitab-pip-config-${hostname}`;
    let pipConfig = sessionStorage.getItem(sessionKey);
    if (pipConfig && Object.keys(pipConfig).length > 0) {
        const parsedConfig = JSON.parse(pipConfig);
        return parsedConfig;
    }

    try {
        const response = await sendRuntimeMessage('get_pip_config', { url: window.location.href });
        console.log("get_pip_config", response);
        if (response.success && response.data) {
            if (Array.isArray(response.data)) {
                const currentUrl = window.location.href;
                for (const dataItem of response.data) {
                    const matches = dataItem.matches;
                    if (typeof matches === 'string' && new RegExp(matches).test(currentUrl)) {
                        config = await processCssConfig(dataItem.css, config);
                        const updatedConfig = { ...config, ...dataItem };
                        sessionStorage.setItem(sessionKey, JSON.stringify(updatedConfig));
                        return { ...updatedConfig, cssUrls: updatedConfig.cssUrls.filter(url => !url.startsWith('http')) };
                    } else if (Array.isArray(matches)) {
                        for (const match of matches) {
                            if (new RegExp(match).test(currentUrl)) {
                                config = await processCssConfig(dataItem.css, config);
                                const updatedConfig = { ...config, ...dataItem };
                                sessionStorage.setItem(sessionKey, JSON.stringify(updatedConfig));
                                return { ...updatedConfig, cssUrls: updatedConfig.cssUrls.filter(url => !url.startsWith('http')) };
                            }
                        }
                    }
                }
            } else {
                config = await processCssConfig(response.data.css, config);
                const updatedConfig = { ...config, ...response.data };
                sessionStorage.setItem(sessionKey, JSON.stringify(updatedConfig));
                return { ...updatedConfig, cssUrls: updatedConfig.cssUrls.filter(url => !url.startsWith('http')) };
            }
        }
    } catch (error) {
        console.error('Error fetching PIP config:', error);
    }

    const commonCaptionConfigs = [
        {
            match: ".vjs-text-track-display",
            nextVideoBtn: '.vjs-icon-next-item',
            supportCaption: true,
            captionBtn: 'vjs-captions-toggle',
            captionParent: 'video-js.video-js',
            caption: '.vjs-text-track-display',
        },
        {
            match: ".jw-captions",
            supportCaption: true,
            supportDanmaku: false,
            captionParent: '.jw-wrapper',
            caption: ".jw-captions",
        },
        {
            match: ".plyr__captions",
            supportCaption: true,
            supportDanmaku: false,
            captionBtn: ".plyr__control[data-plyr=\"captions\"]",
            caption: ".plyr__captions",
            captionStatus: {
                "type": "classList",
                "selector": ".plyr--captions-enabled",
                "className": "plyr--captions-active",
                "include": true
            }
        },
        {
            match: ".text-track-wrapper>.text-track-cue",
            supportCaption: true,
            supportDanmaku: false,
            captionParent: '.text-track-wrapper',
            caption: ".text-track-cue",
        },
        {
            match: ".aliplayer-danmuku",
            nextVideoBtn: '.vjs-icon-next-item',
            supportCaption: true,
            supportDanmaku: true,
            danmakuParent: '.aliplayer-danmuku',
            danmaku: ".danmu.container"
        }
    ]
    let matchCommon = false;
    for (const commonCaptionConfig of commonCaptionConfigs) {
        const matchElement = document.querySelector(commonCaptionConfig.match);
        console.log("commonCaptionConfig matchElement", matchElement);
        if (matchElement) {
            config = { ...config, ...commonCaptionConfig };
            matchCommon = true;
        }
    }

    if (!matchCommon) {
        const detectConfig = detectCaption();
        if (detectConfig) {
            config = { ...config, ...detectConfig, isDetect: true };
        }
    }
    config.isJwPlayer = !!document.querySelector('.jwplayer');


    return { ...config, cssUrls: config.cssUrls.filter(url => !url.startsWith('http')) };
};

const processCssConfig = async (css, config) => {
    if (css) {
        if (Array.isArray(css)) {
            for (const cssUrl of css) {
                await addCssToConfig(cssUrl, config);
            }
        } else {
            await addCssToConfig(css, config);
        }
    }
    console.log("processCssConfig", css, config)

    return config;
};

const addCssToConfig = async (cssUrl, config) => {
    if (cssUrl.startsWith('http')) {
        const result = await sendRuntimeMessage('http_request', {
            url: cssUrl,
            method: 'get'
        });
        if (result.success && result.data) {
            config.styles += `\n${result.data}`;
        }
    }
    return config;
};

const getConfig = async () => {
    if (!config || Object.keys(config).length === 0) {
        config = await initConfig();
        console.log("getConfig", config, settings);
    }

    if (siteConfigs?.captionSetting && siteConfigs?.captionSelector) {
        if (siteConfigs?.captionSelector) {
            config = { ...config, caption: siteConfigs?.captionSelector }
        }
        if (siteConfigs?.captionParentSelector) {
            config = { ...config, captionParent: siteConfigs?.captionParentSelector }
        }
    }


    return config;
}


class Feature {
    constructor(pipWindow, videoContainer, feature) {
        this.config = null;
        this.pipWindow = pipWindow;
        this.videoContainer = videoContainer;
        this.mainEl = null;
        this.feature = feature.toLowerCase();
    }

    async init() {
        if (!this.config) {
            this.config = await getConfig();
        }
    }

    async hasFeature() {
        await this.init();
        const featureKey = `support${this.feature.charAt(0).toUpperCase() + this.feature.slice(1)}`;
        return this.config[featureKey] || (this.config[this.feature] && !!mainWindow.document.querySelector(this.config[this.feature]));
    }

    async isFeatureOn() {
        await this.init();
        const featureStatusKey = `${this.feature}Status`;
        const featureConfig = this.config[featureStatusKey];
        if (featureConfig) {
            const selector = mainWindow.document.querySelector(featureConfig.selector);
            switch (featureConfig.type) {
                case 'attr':
                    return selector?.getAttribute(featureConfig.attrName) === featureConfig.attrValue;
                case 'classList':
                    return selector?.classList.contains(featureConfig.className) === featureConfig.include;
                case 'func':
                    return featureConfig.func();
                case 'childClass':
                    console.log("isFeatureOn childClass", this.feature, featureConfig.selector, selector, featureConfig.childClassName, featureConfig.include, selector?.querySelector(`*[class*="${featureConfig.childClassName}"]`), (!!(selector?.querySelector(`*[class*="${featureConfig.childClassName}"]`))) === featureConfig.include);
                    return (!!(selector?.querySelector(`*[class*="${featureConfig.childClassName}"]`))) === featureConfig.include;
            }
        }
        if (this.feature === 'caption' && siteConfigs?.captionSetting && siteConfigs?.captionSelector) {
            return true;
        }
        if (config.isDetect && config[this.feature]) {
            const featureElement = pipWindow.document.querySelector(config[this.feature]);
            if (featureElement && featureElement.nodeName === 'TRACK') {
                return true;
            }
            const featureText = pipWindow.document.querySelector(config[this.feature])?.textContent.trim();
            return featureText !== "";
        }
        return false;
    }

    async applyFeature() {
        await this.init();
        if (!this.config[this.feature]) return;
        let featureContainer = this.pipWindow.document.querySelector(`.${this.feature}-container`);
        if (!featureContainer) {
            featureContainer = this.pipWindow.document.createElement("div");
            featureContainer.classList.add(`${this.feature}-container`);
            if (!this.config.css) {
                featureContainer.classList.add('enforce-style');
            }
            this.videoContainer.appendChild(featureContainer);
        }
        await waitMoment(500);
        this.mainEl = this.config['wrapperSelector']
            ? originalVideoParent?.closest(this.config['wrapperSelector'])?.querySelector(this.config[this.feature])
            : mainWindow.document.querySelector(this.config[this.feature]);

        if (this.mainEl) {
            saveElementPosition(this.mainEl);
            this.mainEl.style.zIndex = 1999;
            if (this.config.captionStyle) {
                const deepestElement = this.mainEl.querySelector('*:not(:has(*))') || this.mainEl;
                Object.assign(deepestElement.style, this.config.captionStyle);
            } else if (this.feature === 'caption') {
                let deepestElement = this.mainEl;
                while (deepestElement.children.length > 0) {
                    deepestElement = deepestElement.children[0];
                }
                const computedStyle = window.getComputedStyle(deepestElement);
                const style = {
                    display: computedStyle.display,
                    textAlign: computedStyle.textAlign,
                    padding: computedStyle.padding,
                    borderRadius: computedStyle.borderRadius,
                    direction: computedStyle.direction,
                    opacity: computedStyle.opacity,
                    color: computedStyle.color,
                    background: computedStyle.background,
                    backgroundColor: computedStyle.backgroundColor
                };
                Object.assign(deepestElement.style, style);
            }
            featureContainer.appendChild(this.mainEl);
        }
    }

    async resetFeature() {
        if (this.mainEl) {
            await this.init();
            const cleanSelector = this.config[`clean${this.feature.charAt(0).toUpperCase() + this.feature.slice(1)}`];
            if (cleanSelector) {
                const elementsToClean = this.mainEl.querySelectorAll(cleanSelector);
                elementsToClean.forEach(element => element.remove());
            }

            restoreElementPosition(this.mainEl);
        }
    }
}


async function setupFeatures() {
    if (!pipWindow) return;

    danmakuInstance = new Feature(pipWindow, videoContainer, 'danmaku');
    captionInstance = new Feature(pipWindow, videoContainer, 'caption');
    danmakuInstance.applyFeature();
    captionInstance.applyFeature();

    return {
        danmakuInstance,
        captionInstance,
    };
}

async function resetFeatures() {
    if (!pipWindow) return;
    if (danmakuInstance) {
        danmakuInstance.resetFeature();
    }
    if (captionInstance) {
        captionInstance.resetFeature();
    }
}

function setInnerHTML(element, html) {
    if (element) {
        element.innerHTML = '';
        element.innerHTML = html;
    }
}


const isShowLoading = () => {
    if (!pipWindow) return false;
    const loadingContainer = pipWindow.document.querySelector('.loading-container');
    return loadingContainer ? loadingContainer.classList.contains('show') : false;
}

const setLoading = (isLoading, loadingText = "Loading...") => {
    if (!pipWindow) return;
    const loadingContainer = pipWindow.document.querySelector('.loading-container');
    if (loadingContainer) {
        const loadingTextElement = loadingContainer.querySelector('.loading-text');
        if (loadingTextElement) {
            loadingTextElement.textContent = loadingText;
        }
        if (isLoading) {
            loadingContainer.classList.add('show');
        } else {
            loadingContainer.classList.remove('show');
        }
    }
}

let isShowPlayIcon = false;
const setPlayIcon = (isShow) => {
    if (!pipWindow) return;
    const loadingContainer = pipWindow.document.querySelector('.icon-play-container');
    if (loadingContainer) {
        if (isShow) {
            loadingContainer.classList.add('show');
            isShowPlayIcon = true;
        } else {
            loadingContainer.classList.remove('show');
            isShowPlayIcon = false;
        }
    }
}


const updateRangeStyle = (rangeElement, setValue = null) => {
    const value = setValue ? (setValue + "%") : rangeElement.value + "%";
    rangeElement.style.setProperty("--litters-range", value);
}

const addEventListenerToButton = (selector, event, handler) => {
    const button = pipWindow.document.querySelector(selector);
    if (button) {
        const newHandler = handler.bind(button);
        button.removeEventListener(event, newHandler);
        button.addEventListener(event, newHandler);
    }
};

const updateButtonSvg = (selector, svg) => {
    if (!pipWindow) return;
    const button = typeof selector === 'string' ? pipWindow.document.querySelector(selector) : selector;
    if (button) {
        button.innerHTML = svg;
    }
};


const pipVideo = () => {
    if (!pipWindow) return null;
    return pipWindow.document.querySelector('video,smp-toucan-player');
}

async function createVideoControls(pipWindow, container) {
    if (!pipWindow || !container) return;

    let videoElement = pipVideo();
    const config = await getConfig();
    const mainPrevVideoBtnEl = config.prevVideoBtn ? mainWindow.document.querySelector(config.prevVideoBtn) : null;
    const mainNextVideoBtnEl = config.nextVideoBtn ? mainWindow.document.querySelector(config.nextVideoBtn) : null;

    if (!pipWindow.document.querySelector("#player-control-container")) {
        const modal = pipWindow.document.createElement("div");
        modal.id = "player-control-container";
        setInnerHTML(modal, `
            <div class="controls">
                <div class="control-row">
                    <input id="videoPlayingProgress" type="range" min="0" max="100" step="0.1" value="0" />
                </div>
                <div class="control-row second-row">
                    ${mainPrevVideoBtnEl ? `<div class="control-group control-group-next">
                        <button data-action="prev" title="${chrome.i18n.getMessage("prev_video")}">
                            ${prevSvg}
                        </button>
                    </div>` : ""}
                    <div class="control-group">
                        <button data-action="play" title="${chrome.i18n.getMessage("play")} / ${chrome.i18n.getMessage("pause")}">
                            ${videoElement?.paused ? playSvg : pauseSvg}
                        </button>
                    </div>
                    ${mainNextVideoBtnEl ? `<div class="control-group control-group-next">
                        <button data-action="next" title="${chrome.i18n.getMessage("next_video")}">
                            ${nextSvg}
                        </button>
                    </div>` : ""}
                    <div class="control-group">
                        <button data-action="mute" title="${chrome.i18n.getMessage("mute")} / ${chrome.i18n.getMessage("unmute")}" class="pip-unmuted">
                           ${videoElement?.muted ? soundDisabledSvg : videoElement?.volume === 0 ? muteSvg : unmuteSvg}
                        </button>
                        <input id="videoVolumeProgress" type="range" min="0" max="100" />
                    </div>
                    <div class="control-group control-group-time">
                        <div class="control-time"></div>
                    </div>
                    <div class="control-group align-right">
                        <button data-action="playbackRate" title="${chrome.i18n.getMessage("playbackRate")}">
                            <div class="player-ctrl-playbackrate-result">1x</div>
                        </button>
                        <ul class="player-ctrl-playbackrate-menu">
                            <li class="player-ctrl-playbackrate-menu-item" data-value="4">4.0x</li>
                            <li class="player-ctrl-playbackrate-menu-item" data-value="3">3.0x</li>
                            <li class="player-ctrl-playbackrate-menu-item" data-value="2">2.0x</li>
                            <li class="player-ctrl-playbackrate-menu-item" data-value="1.75">1.75x</li>
                            <li class="player-ctrl-playbackrate-menu-item" data-value="1.5">1.5x</li>
                            <li class="player-ctrl-playbackrate-menu-item" data-value="1.25">1.25x</li>
                            <li class="player-ctrl-playbackrate-menu-item bpx-state-active" data-value="1">1.0x</li>
                            <li class="player-ctrl-playbackrate-menu-item" data-value="0.75">0.75x</li>
                            <li class="player-ctrl-playbackrate-menu-item" data-value="0.5">0.5x</li>
                        </ul>
                    </div>
                    ${await danmakuInstance?.hasFeature() ? `<div class="control-group">
                        <button data-action="danmaku" title="${chrome.i18n.getMessage("danmaku")}">
                            ${danmakuSvg}
                        </button>
                    </div>` : ""}
                    ${await captionInstance?.hasFeature() ? `<div class="control-group">
                        <button data-action="caption" title="${chrome.i18n.getMessage("subtitles")} / ${chrome.i18n.getMessage("close_captions")}">
                            ${captionSvg}
                        </button>
                    </div>` : ""}
                    <div class="control-group">
                        <button data-action="fit" title="${chrome.i18n.getMessage("fit_to_screen")}">
                            ${fitToScreenSvg}
                        </button>
                    </div>
                      <div class="control-group">
                        <button data-action="shortcuts" title="${chrome.i18n.getMessage("shortcuts")}">
                            ${keyboardSvg}
                        </button>
                    </div>
                </div>
            </div>
        `);
        container.append(modal);

        // Add event listeners for buttons
        addEventListenerToButton(`#player-control-container button[data-action="shortcuts"]`, 'click', function (event) {
            const shortcutsContainer = pipWindow.document.querySelector(".player-shortcuts-container");
            if (shortcutsContainer) {
                shortcutsContainer.classList.toggle('hidden');
            }
        });
        addEventListenerToButton(`#player-control-container button[data-action="play"]`, 'click', function (event) {
            const currentVideo = pipVideo();
            currentVideo?.paused ? playVideo(currentVideo) : pauseVideo(currentVideo);
            updateButtonSvg(event.target, currentVideo?.paused ? playSvg : pauseSvg);
        });

        addEventListenerToButton(`#player-control-container button[data-action="prev"]`, 'click', async function () {
            isWaiting = true;
            mainPrevVideoBtnEl?.click();
            setLoading(true);
        });

        addEventListenerToButton(`#player-control-container button[data-action="next"]`, 'click', async function () {
            isWaiting = true;
            mainNextVideoBtnEl?.click();
            setLoading(true);
        });

        addEventListenerToButton(`#player-control-container button[data-action="mute"]`, 'click', function (event) {
            const currentVideo = pipVideo();
            switchVideoMute(currentVideo)
            updateButtonSvg(event.target, currentVideo?.muted ? soundDisabledSvg : currentVideo?.volume === 0 ? muteSvg : unmuteSvg);
            const volumeProgressBar = pipWindow.document.querySelector("#player-control-container #videoVolumeProgress");
            if (volumeProgressBar) {
                volumeProgressBar.value = Math.floor(currentVideo?.volume * 1000) / 10;
                updateRangeStyle(volumeProgressBar);
            }
        });
        addEventListenerToButton(`#player-control-container button[data-action="fit"]`, 'click', function () {
            const currentVideo = pipVideo();
            if (!pipWindow || !currentVideo) return;
            const videoWidth = currentVideo.videoWidth;
            const videoHeight = currentVideo.videoHeight;
            const aspectRatio = videoWidth / videoHeight;
            const newHeight = Math.floor(pipWindow.innerWidth / aspectRatio + 0.5);
            pipWindow.resizeBy(0, newHeight - pipWindow.innerHeight);
            const isPortraitMode = isPortrait(videoWidth, videoHeight);
            const currentWidth = pipWindow.innerWidth;

            if (isPortraitMode) {
                localStorage.setItem('pipPortraitWidth', currentWidth);
            } else {
                localStorage.setItem('pipLandscapeWidth', currentWidth);
            }
        });

        // Set up volume progress bar
        const volumeProgressBar = pipWindow.document.querySelector("#player-control-container #videoVolumeProgress");
        if (volumeProgressBar) {
            volumeProgressBar.value = Math.floor(videoElement?.volume * 1000) / 10;
            updateRangeStyle(volumeProgressBar);
        }

        addEventListenerToButton("#player-control-container #videoVolumeProgress", "input", function (event) {
            const currentVideo = pipVideo();
            if (currentVideo) {
                currentVideo.volume = event.target.value / 100;
                updateRangeStyle(event.target);
                updateButtonSvg('#player-control-container button[data-action="mute"]', currentVideo?.muted ? soundDisabledSvg : currentVideo?.volume === 0 ? muteSvg : unmuteSvg);
            }
        });

        // Set up video progress bar
        const videoProgressBar = pipWindow.document.querySelector("#player-control-container #videoPlayingProgress");
        if (videoProgressBar) {
            const duration = isDisneyplus() ? getDisneyVideoDuration() || videoElement?.duration : videoElement?.duration;
            videoProgressBar.value = Math.floor((videoElement?.currentTime / duration) * 1000) / 10;
            updateRangeStyle(videoProgressBar);
            // Set up progress bar tooltip
            videoProgressBar.addEventListener("mousemove", function (event) {
                const currentVideo = pipVideo();
                const progress = calcSliderPos(event);
                const duration = isDisneyplus() ? getDisneyVideoDuration() || currentVideo?.duration : currentVideo?.duration;
                const hoverTime = Math.floor(duration * (progress / 100));
                let tooltip = pipWindow.document.querySelector("#progress-tooltip");
                if (!tooltip) {
                    tooltip = pipWindow.document.createElement("div");
                    tooltip.id = "progress-tooltip";
                    tooltip.style.position = "fixed";
                    tooltip.style.background = "#000";
                    tooltip.style.color = "#fff";
                    tooltip.style.padding = "2px 5px";
                    tooltip.style.borderRadius = "3px";
                    tooltip.style.fontSize = "10px";
                    tooltip.style.pointerEvents = "none";
                    tooltip.style.zIndex = "9999";
                    pipWindow.document.body.appendChild(tooltip);
                }
                tooltip.textContent = formatDuration(hoverTime);
                const rect = videoProgressBar.getBoundingClientRect();
                const tooltipRect = tooltip.getBoundingClientRect();
                let left = event.clientX - tooltipRect.width / 2;
                left = Math.max(rect.left, Math.min(left, rect.right - tooltipRect.width));
                tooltip.style.left = `${left}px`;
                tooltip.style.top = `${rect.top - tooltipRect.height - 5}px`;
            });

            videoProgressBar.addEventListener("mouseleave", function () {
                const tooltip = pipWindow.document.querySelector("#progress-tooltip");
                if (tooltip) {
                    tooltip.remove();
                }
            });
        }

        addEventListenerToButton("#player-control-container #videoPlayingProgress", "input", function (event) {
            const currentVideo = pipVideo();
            if (!currentVideo || currentVideo?.duration === Infinity || !isNumber(currentVideo?.duration)) {
                return
            }
            isSeeking = true;
            setTimeout(() => {
                isSeeking = false;
                if (currentVideo?.paused) {
                    playVideo(currentVideo);
                }
            }, 5000);
            const duration = isDisneyplus() ? getDisneyVideoDuration() || currentVideo?.duration : currentVideo?.duration;
            const newTime = Math.round(duration * (parseInt(event.target.value, 10) / 100));
            setLoading(true);
            updateRangeStyle(event.target, event.target.value);
            seekVideo(newTime, currentVideo)
        });



        setupFeatureButton(pipWindow, 'danmaku');
        setupFeatureButton(pipWindow, 'caption');
        setupPlaybackRateMenu(pipWindow);
        setupEventListeners(pipWindow);
    }

    const setupVideoElementListeners = (videoElement) => {
        if (!videoElement) return;

        const listeners = {
            'play': function () {
                isSeeking = false;
                if (isShowPlayIcon) {
                    setPlayIcon(false);
                }
                setLoading(false);
                updateButtonSvg('#player-control-container button[data-action="play"]', this.paused ? playSvg : pauseSvg);
            },
            'pause': function () {
                setPlayIcon(true);
                updateButtonSvg('#player-control-container button[data-action="play"]', this.paused ? playSvg : pauseSvg);
            },
            'seeked': function () {
                isSeeking = false;
            },
            'progress': function (event) {
                setLoading(event.target.readyState < 3);
            },
            'ended': function () {
                if (!settings?.continuousPlayback) {
                    exitDocumentPip();
                    return;
                }
                if (!isCloseing) {
                    setLoading(true);
                    console.warn("video ended", originalVideoParent, this)
                    tracker.restoreVideoPosition(this);
                    // if (originalVideoParent) {
                    //     originalVideoParent.append(this);
                    //     originalVideoParent = null;
                    // }
                    resetFeatures();
                    // nextVideo();
                    setTimeout(() => {
                    }, 500)
                }
                // nextVideo();
            },
            'timeupdate': function () {
                const duration = isDisneyplus() ? getDisneyVideoDuration() || this.duration : this.duration;

                if (isSeeking) return;

                if (isShowLoading() && duration - this.currentTime >= 5) {
                    setLoading(false);
                }
                if (isShowPlayIcon) {
                    setPlayIcon(false);
                }

                const progressBar = pipWindow.document.querySelector("#player-control-container #videoPlayingProgress");
                if (progressBar) {
                    progressBar.value = Math.floor((this.currentTime / duration) * 1000) / 10;
                    updateRangeStyle(progressBar);
                }

                const videoDurationEl = pipWindow.document.querySelector("#player-control-container .control-time");
                if (videoDurationEl) {
                    const currentTimeText = formatDuration(this.currentTime);
                    const durationText = !duration || duration === Infinity ? '' : `${currentTimeText} / ${formatDuration(duration)}`;
                    setInnerHTML(videoDurationEl, `<span>${durationText}</span>`);
                }
            },
            'volumechange': function () {
                updateButtonSvg('#player-control-container button[data-action="mute"]', this.muted || this.volume === 0 ? muteSvg : unmuteSvg);
                const volumeProgressBar = pipWindow.document.querySelector("#player-control-container #videoVolumeProgress");
                if (volumeProgressBar) {
                    volumeProgressBar.value = Math.floor(this.volume * 1000) / 10;
                    updateRangeStyle(volumeProgressBar);
                }
            },
            'ratechange': function () {
                const videoPlaybackRateEl = pipWindow.document.querySelector("#player-control-container .player-ctrl-playbackrate-result");
                const formattedRate = (Math.round(this.playbackRate * 4) / 4).toFixed(2).replace(/\.?0+$/, '');
                setInnerHTML(videoPlaybackRateEl, `<span>${formattedRate}x</span>`);
            },
            'dblclick': function (event) {
                event.stopPropagation();
                this.paused ? this.play() : this.pause();
            }
        };

        Object.entries(listeners).forEach(([event, listener]) => {
            videoElement.removeEventListener(event, listener);
            videoElement.addEventListener(event, listener);
        });
    };

    setupVideoElementListeners(videoElement);

    // Re-setup listeners when video element changes
    const videoObserver = new MutationObserver((mutations) => {
        mutations.forEach((mutation) => {
            if (mutation.type === 'childList') {
                const newVideoElement = pipVideo();
                if (newVideoElement && newVideoElement !== videoElement) {
                    videoElement = newVideoElement;
                    setupVideoElementListeners(videoElement);
                }
            }
        });
    });

    videoObserver.observe(pipWindow.document.body, { childList: true, subtree: true });
}

// Add event listener for ALT+P shortcut to toggle Picture-in-Picture mode

function setupEventListeners(pipWindow) {
    const handleKeydown = function (event) {
        event.stopPropagation();
        const currentVideo = pipVideo();
        if (!currentVideo) return;
        const duration = isDisneyplus() ? getDisneyVideoDuration() || currentVideo.duration : currentVideo.duration;
        let videoContainer = pipWindow.document.querySelector(".video-container");
        const nextBtn = pipWindow.document.querySelector('#player-control-container button[data-action="next"]');
        const prevBtn = pipWindow.document.querySelector('#player-control-container button[data-action="prev"]');
        const percent = parseInt(event.code.slice(-1)) * 10;

        switch (event.code) {
            case 'Space':
            case 'KeyK':
                currentVideo.paused ? playVideo(currentVideo) : pauseVideo(currentVideo);
                break;
            case 'ArrowLeft':
                setLoading(true);
                seekVideo(Math.max(0, Math.floor(currentVideo.currentTime - 5)), currentVideo);
                break;
            case 'ArrowRight':
                setLoading(true);
                seekVideo(Math.min(Math.floor(duration), Math.floor(currentVideo.currentTime + 5)), currentVideo);
                break;
            case 'KeyJ':
                setLoading(true);
                seekVideo(Math.max(0, Math.floor(currentVideo.currentTime - 10)), currentVideo);
                break;
            case 'KeyL':
                setLoading(true);
                seekVideo(Math.min(Math.floor(duration), Math.floor(currentVideo.currentTime + 10)), currentVideo);
                break;
            case 'ArrowUp':
                volumeVideo(Math.min(1, currentVideo.volume + 0.05), currentVideo);
                break;
            case 'ArrowDown':
                volumeVideo(Math.max(0, currentVideo.volume - 0.05), currentVideo);
                break;
            case 'Escape':
                exitDocumentPip();
                break;
            case 'KeyQ':
                if (videoContainer) {
                    videoContainer.classList.toggle("hidden");
                    currentVideo.paused ? currentVideo.play() : currentVideo.pause();
                }
                break;
            case 'KeyM':
                switchVideoMute(currentVideo);
                break;
            case 'KeyN':
                if (event.shiftKey) {
                    nextBtn ? nextBtn.click() : nextVideo();
                }
                break;
            case 'KeyP':
                if (event.shiftKey) {
                    prevBtn ? prevBtn.click() : prevVideo();
                } else if (event.altKey) {
                    exitDocumentPip();
                }
                break;
            case 'KeyT':
                pipWindow.document.querySelector('#player-control-container button[data-action="fit"]')?.click();
                break;
            case 'KeyC':
                pipWindow.document.querySelector('#player-control-container button[data-action="caption"]')?.click();
                break;
            case 'KeyB':
                pipWindow.document.querySelector('#player-control-container button[data-action="danmaku"]')?.click();
                break;
            case 'KeyF':
                exitDocumentPip();

                setTimeout(() => {
                    const mainVideo = tracker?.currentMainVideo || document.querySelector('video');
                    if (mainVideo && mainVideo.requestFullscreen) {
                        mainVideo.requestFullscreen();
                    }
                }, 200);
                // window.focus();
                break;
            case 'Period':
                if (currentVideo) {
                    currentVideo.playbackRate = Math.min(4, currentVideo.playbackRate + 0.25);
                }
                break;
            case 'Comma':
                if (currentVideo) {
                    currentVideo.playbackRate = Math.max(0.25, currentVideo.playbackRate - 0.25);
                }
                break;
            case 'Delete':
                if (currentVideo) {
                    currentVideo.playbackRate = 4;
                }
                break;
            case 'Home':
                seekVideo(0, currentVideo);
                break;
            case 'End':
                seekVideo(duration, currentVideo);
                break;
            case 'Digit0':
            case 'Digit1':
            case 'Digit2':
            case 'Digit3':
            case 'Digit4':
            case 'Digit5':
            case 'Digit6':
            case 'Digit7':
            case 'Digit8':
            case 'Digit9':
                seekVideo(duration * (percent / 100), currentVideo);
                break;
        }
    };

    const handleWheel = function (event) {
        event.stopPropagation();
        const currentVideo = pipVideo();
        if (!currentVideo) return;
        const duration = isDisneyplus() ? getDisneyVideoDuration() || currentVideo.duration : currentVideo.duration;
        if (event.deltaY < 0) {
            // Scroll up
            setLoading(true);
            seekVideo(Math.max(0, Math.floor(currentVideo.currentTime - 5)), currentVideo);
        } else if (event.deltaY > 0) {
            // Scroll down
            setLoading(true);
            seekVideo(Math.min(Math.floor(duration), Math.floor(currentVideo.currentTime + 5)), currentVideo);
        }
    };

    addEventListenersOnce(pipWindow.document, 'keydown', handleKeydown);
    addEventListenersOnce(pipWindow.document, 'wheel', handleWheel);
}




