async function setupFeatureButton(pipWindow, feature) {
    const config = await getConfig();
    feature = feature.toLowerCase()
    const featureContainer = pipWindow.document.querySelector(`.${feature}-container`);
    const featureButton = pipWindow.document.querySelector(`button[data-action="${feature}"]`);
    if (!featureButton) return;
    const featureInstance = feature === 'caption' ? captionInstance : feature === 'danmaku' ? danmakuInstance : null;
    if (!featureInstance) return;

    const isJwCaptionFeature = config.isJwPlayer && feature === 'caption';
    const isFeatureOn = isJwCaptionFeature
        ? pipWindow.document.querySelector('.jw-captions')?.classList.contains('jw-captions-enabled')
        : await featureInstance.isFeatureOn();

    if (isFeatureOn) {
        featureButton.classList.add("active");
        isJwCaptionFeature ? pipWindow.document.querySelector('.jw-captions')?.classList.remove("hidden") : featureContainer?.classList.remove("hidden");
    } else {
        featureButton.classList.remove("active");
        isJwCaptionFeature ? pipWindow.document.querySelector('.jw-captions')?.classList.add("hidden") : featureContainer?.classList.add("hidden");
    }

    featureButton.addEventListener("click", () => {

        if (config.isJwPlayer && feature === 'caption') {
            pipWindow.document.querySelector('.jw-captions')?.classList.toggle('hidden');
            featureButton.classList.toggle("active");
        } else if (config.isDetect && config[feature]) {
            pipWindow.document.querySelector(config[feature])?.classList.toggle('hidden');
            featureButton.classList.toggle("active");
            featureContainer?.classList.toggle("hidden");

        } else {
            const featureConfigBtn = config[`${feature}Btn`];
            if (featureConfigBtn) {
                mainWindow.document.querySelector(featureConfigBtn)?.click();
            }
            setTimeout(() => {
                featureButton.classList.toggle("active");
                featureContainer?.classList.toggle("hidden");
                if (featureContainer && !featureContainer.classList.contains('hidden')) {
                    featureInstance.applyFeature();
                }
            }, 200);
        }


    });
}

function setupPlaybackRateMenu(pipWindow) {
    if (!pipWindow) return;
    const videoElement = pipVideo();
    const playbackRateButton = pipWindow.document.querySelector('button[data-action="playbackRate"]');
    const playbackRateMenu = pipWindow.document.querySelector('.player-ctrl-playbackrate-menu');
    const playbackRateResult = pipWindow.document.querySelector('.player-ctrl-playbackrate-result');

    playbackRateButton?.addEventListener("click", (event) => {
        event.stopPropagation();
        playbackRateMenu.style.display = playbackRateMenu.style.display === "none" ? "block" : "none";
    });

    playbackRateMenu?.addEventListener("click", (event) => {
        if (event.target.classList.contains("player-ctrl-playbackrate-menu-item")) {
            const newRate = parseFloat(event.target.getAttribute("data-value"));
            videoElement.playbackRate = newRate;
            playbackRateResult.textContent = `${newRate}x`;
            playbackRateMenu.style.display = "none";

            // Update active state
            playbackRateMenu.querySelectorAll('.player-ctrl-playbackrate-menu-item').forEach(item => {
                item.classList.toggle('bpx-state-active', item === event.target);
            });
        }
    });

    // Hide playback rate menu when clicking outside
    pipWindow.document.addEventListener("click", () => {
        playbackRateMenu.style.display = "none";
    });
}


function escapeHTML(html) {
    const div = document.createElement('div');
    div.innerHTML = html;
    return div.innerHTML;
}


const isPortrait = (width, height) => {
    return height > width;
}

async function createPlayer() {

    if (location.hostname === 'www.tiktok.com') {
        let divM = Array.from(document.querySelectorAll('video')).find(video => !video.paused)?.closest('[class*="DivVideoPlayerContainer"]')?.querySelector('[class*="DivMiniPlayerButtonContainer "]');
        if (divM) {
            divM.click();
            return false;
        }
    }

    const currentPipVideo = pipVideo();
    if (currentPipVideo && !currentPipVideo.ended) {
        return false;
    }
    setVideoPlaceholderOverlay(chrome.i18n.getMessage('loading_pip_config'), 3000);
    const config = await getConfig();
    console.log("createPlayer config", config);
    let playerElement, videoElement;

    if (config.isJwPlayer) {
        playerElement = document.querySelector(".jwplayer");

        if (isWaiting && !playerElement) {
            console.error("JW Player element not found");
            removeVideoPlaceholderOverlay();
            toastMessage(chrome.i18n.getMessage("video_not_detected"));
            return false;
        }
        saveElementPosition(playerElement);
    } else {
        videoElement = tracker?.currentMainVideo;
        if (isWaiting && !videoElement) {
            return false;
        }
        if (!videoElement) {
            console.error("Video element not found");
            removeVideoPlaceholderOverlay();
            toastMessage(chrome.i18n.getMessage("video_not_detected"));
            return false;
        }
    }

    originalVideoParent = config.isJwPlayer ? playerElement.parentElement : tracker.currentMainVideoWrapper;
    console.log('originalParent', originalVideoParent, videoElement);

    if (!documentPictureInPicture.window) {
        const { width, height } = calculatePipDimensions(videoElement || playerElement, config.isJwPlayer);

        try {
            pipWindow = await documentPictureInPicture.requestWindow({ width, height });
            setupPipWindowListeners(pipWindow, config.isJwPlayer);
            await applyStyles(pipWindow, config);
            createPipWindowElements(pipWindow, config.isJwPlayer);
        } catch (error) {
            pipWindow = null;
            console.error("documentPictureInPicture requestWindow error", error);
            setVideoPlaceholderOverlay(await createOverlayErrorMessage(error), 0, document.body);
            return false;
        }
    } else {
        pipWindow = documentPictureInPicture.window;
    }

    await waitMoment(100);
    if (config.isJwPlayer) {
        videoContainer.append(playerElement);
    } else {
        appendVideoToPipWindow(videoElement);
    }
    createVideoTitle();
    await setupFeatures();
    await createVideoControls(pipWindow, videoContainer);
    setVideoPlaceholderOverlay(chrome.i18n.getMessage('playing_in_pip_mode'), 0, originalVideoParent);
    isWaiting = false;
    return true;
}

function calculatePipDimensions(element, isJwPlayer) {
    const screenWidth = Math.floor(window.screen.width / window.devicePixelRatio * 0.4);
    const aspectRatio = isJwPlayer ?
        (element.clientWidth / element.clientHeight) :
        ((element.videoWidth || element.clientWidth) / (element.videoHeight || element.clientHeight));
    const elementWidth = isJwPlayer ? element.clientWidth : (element.videoWidth || element.clientWidth);
    const elementHeight = isJwPlayer ? element.clientHeight : (element.videoHeight || element.clientHeight);
    const isPortraitMode = isPortrait(elementWidth, elementHeight);
    const storedPortraitWidth = localStorage.getItem('pipPortraitWidth');
    const storedLandscapeWidth = localStorage.getItem('pipLandscapeWidth');

    const maxWidth = Math.min(screenWidth, isPortraitMode ? 430 : 480);
    const width = (isPortraitMode ? storedPortraitWidth : storedLandscapeWidth) || Math.min(elementWidth, maxWidth) || (isPortraitMode ? 430 : 480);
    const height = width / aspectRatio || (isPortraitMode ? 930 : 270);

    console.log("calculatePipDimensions", width, height, element)
    return { width, height };
}

function setupPipWindowListeners(pipWindow, isJwPlayer) {
    pipWindow.addEventListener("pagehide", (event) => {

        tracker?.currentMainVideoWrapper?.scrollIntoView();

        isCloseing = true;
        try {

            if (isJwPlayer) {
                const pipElement = event.target.querySelector(".jwplayer");
                restoreElementPosition(pipElement);
            } else {
                tracker.restoreVideoPosition(event.target.querySelector("video"))
                resetFeatures();
            }
            removeVideoPlaceholderOverlay();

        } catch (error) {
            console.error("Error during pagehide event:", error);
        } finally {
            pipWindow = null;
            delay(() => { sendPictureInPictureInfo() }, 500);
        }
    });
}

async function applyStyles(pipWindow, config) {
    const applyStylesheet = (href) => {
        return new Promise((resolve) => {
            if (!pipWindow.document.querySelector(`link[href="${href}"]`)) {
                const linkElement = pipWindow.document.createElement("link");
                linkElement.rel = "stylesheet";
                linkElement.href = href;
                linkElement.onload = resolve;
                pipWindow.document.head.appendChild(linkElement);
            } else {
                resolve();
            }
        });
    };

    const stylePromises = [];

    if (config.isJwPlayer) {
        stylePromises.push(applyStylesheet(chrome.runtime.getURL("css/jwplayer.css")));
    }

    if (Array.isArray(config.localCss) && config.localCss.length > 0) {
        stylePromises.push(...config.localCss.map(url => applyStylesheet(chrome.runtime.getURL(url))));
    }

    if (config.styles && typeof config.styles === 'string' && config.styles.trim() !== '') {
        const styleElement = pipWindow.document.createElement('style');
        styleElement.textContent = config.styles.trim();
        pipWindow.document.head.appendChild(styleElement);
    }

    await Promise.all(stylePromises);
}

function createPipWindowElements(pipWindow, isJwPlayer) {
    createLoadingContainer(pipWindow);
    if (!isJwPlayer) {
        createPlayIconContainer(pipWindow);
    }
    createVideoContainer(pipWindow);
    createVideoTitle(pipWindow);
    createShortcutsContainer(pipWindow);
}

