function createShortcutsContainer(pipWindow) {
    let shortcutsContainer = pipWindow.document.querySelector(".player-shortcuts-container");
    if (!shortcutsContainer) {
        shortcutsContainer = pipWindow.document.createElement("div");
        shortcutsContainer.classList.add("player-shortcuts-container", "hidden");

        const closeButton = pipWindow.document.createElement("button");
        closeButton.classList.add("close-btn");
        closeButton.innerHTML = '&times;';
        closeButton.addEventListener("click", () => {
            shortcutsContainer.classList.add("hidden");
        });

        const shortcutsContent = pipWindow.document.createElement("div");
        shortcutsContent.classList.add("shortcuts");
        const shortcuts = [
            { keys: "Space / k", description: chrome.i18n.getMessage("shortcut_play_pause") },
            { keys: "Left/Right Arrow", description: chrome.i18n.getMessage("shortcut_fast_forward_rewind_5") },
            { keys: "J / L", description: chrome.i18n.getMessage("shortcut_fast_forward_rewind_10") },
            { keys: "Up/Down Arrow", description: chrome.i18n.getMessage("shortcut_adjust_volume") },
            { keys: "M", description: chrome.i18n.getMessage("shortcut_mute_unmute") },
            { keys: "Mouse Wheel", description: chrome.i18n.getMessage("shortcut_fast_forward_rewind") },
            { keys: "Shift+N", description: chrome.i18n.getMessage("shortcut_next_video") },
            { keys: "Shift+P", description: chrome.i18n.getMessage("shortcut_previous_video") },
            { keys: "> / <", description: chrome.i18n.getMessage("shortcut_playback_speed") },
            { keys: "B", description: chrome.i18n.getMessage("shortcut_toggle_danmaku") },
            { keys: "C", description: chrome.i18n.getMessage("shortcut_toggle_captions") },
            { keys: "T", description: chrome.i18n.getMessage("shortcut_fit_to_screen") },
            { keys: "Q", description: chrome.i18n.getMessage("shortcut_toggle_hide_show") },
            { keys: "F", description: chrome.i18n.getMessage("shortcut_toggle_fullscreen") },
            { keys: "Home / End", description: chrome.i18n.getMessage("shortcut_seek_video_start_end") },
            { keys: "0-9", description: chrome.i18n.getMessage("shortcut_seek_percentage") },
            { keys: "Alt+P", description: chrome.i18n.getMessage("shortcut_enter_pip") },
            { keys: "Esc", description: chrome.i18n.getMessage("shortcut_exit_pip") },
        ];

        shortcuts.forEach(shortcut => {
            const shortcutElement = pipWindow.document.createElement("div");
            shortcutElement.classList.add("shortcut");

            const keysElement = pipWindow.document.createElement("div");
            keysElement.classList.add("keys");
            keysElement.textContent = shortcut.keys;

            const descriptionElement = pipWindow.document.createElement("div");
            descriptionElement.classList.add("description");
            descriptionElement.textContent = shortcut.description;

            shortcutElement.append(keysElement, descriptionElement);
            shortcutsContent.append(shortcutElement);
        });

        shortcutsContainer.append(closeButton, shortcutsContent);
        pipWindow.document.body.appendChild(shortcutsContainer);

        // 
        shortcutsContainer.addEventListener('wheel', (event) => {
            event.stopPropagation();
        }, { passive: false });
    }
}

function appendVideoToPipWindow(videoElement) {
    const existingVideo = videoContainer?.querySelector('video,smp-toucan-player');
    if (existingVideo) {
        existingVideo.remove();
    }

    if (!videoElement) {
        videoElement = document.querySelector('video,smp-toucan-player');
    }

    if (videoElement) {
        videoContainer.append(videoElement);
    } else {
        console.error("No video element found to append to PiP window");
    }
}

export async function exitDocumentPip() {
    tracker?.currentMainVideoWrapper?.scrollIntoView();
    exitPictureInPicture()

    if (!isSupportDocumentPictureInPicture()) {
        return
    }


    const config = await getConfig();
    isCloseing = true;
    if (documentPictureInPicture.window) {
        const doc = documentPictureInPicture.window.document;
        const element = config.isJwPlayer ? doc.querySelector(".jwplayer") : doc.querySelector("video");

        if (element) {
            config.isJwPlayer ? restoreElementPosition(element) : tracker.restoreVideoPosition(element);
        }

        resetFeatures();
        pipWindow = null;
        delay(sendPictureInPictureInfo, 500);
        documentPictureInPicture.window.close();
    }
}


export async function enterDocumentPip(force = false) {
    if (!force && !document.querySelector('video')) {
        return false;
    }

    if (!settings.advancedPipEnable) {
        return activeVideoEnterPictureInPicture();
    }

    isCloseing = false;

    if (force) {
        const mainVideo = await getMainVideo();
        if (mainVideo) {
            await new Promise((resolve) => {
                const maxWaitTime = 10000;
                const startTime = Date.now();

                const checkVideoReady = () => {
                    if (mainVideo.readyState === 4 || Date.now() - startTime >= maxWaitTime) {
                        if (navigator.userActivation.isActive) {
                            playVideo(mainVideo);
                        } else {
                            mainVideo.muted = true;
                            playVideo(mainVideo);
                        }
                        tracker.currentMainVideo = mainVideo;
                        resolve();
                    } else {
                        setTimeout(checkVideoReady, 100);
                    }
                };

                checkVideoReady();
            });
        }
    }

    return createPlayer();
}

export async function playingVideoEnterDocumentPip() {
    const currentVideo = tracker.currentMainVideo;
    if (!currentVideo || currentVideo.paused || currentVideo.muted || currentVideo.volume === 0) {
        return false;
    }
    await enterDocumentPip();
}

export async function togglePictureInPicture() {
    if (!isSupportDocumentPictureInPicture()) {
        return
    }
    if (documentPictureInPicture.window) {
        exitDocumentPip();
        return;
    }
    enterDocumentPip();
}





const toastMessage = (message, duration = 3000) => {
    const toastId = `toast-${Date.now()}`;
    const toast = document.createElement('div');
    toast.id = toastId;
    toast.style.cssText = `
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-color: rgba(0, 0, 0, 0.75);
        color: white;
        padding: 25px;
        border-radius: 5px;
        z-index: 10000;
        text-align: center;
        pointer-events: none;
        opacity: 0;
        transition: opacity 0.5s ease;
        font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,Noto Sans,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol,Noto Color Emoji;
        font-size: 14px;
    `;
    toast.textContent = message;
    document.body.appendChild(toast);

    // Trigger reflow to ensure the initial state is applied before transitioning
    toast.offsetHeight;

    // Show the toast with a fade-in effect
    requestAnimationFrame(() => {
        toast.style.opacity = '1';
    });

    setTimeout(() => {
        const existingToast = document.getElementById(toastId);
        if (existingToast) {
            existingToast.style.opacity = '0';
            existingToast.addEventListener('transitionend', function handler() {
                if (document.body.contains(existingToast)) {
                    document.body.removeChild(existingToast);
                }
                existingToast.removeEventListener('transitionend', handler);
            });
        }
    }, duration);
}

let placeholderOverlay = null;

const setVideoPlaceholderOverlay = (content, duration = 0, parent = null) => {
    if (placeholderOverlay) {
        placeholderOverlay.remove();
    }

    if (!content) return;

    placeholderOverlay = document.createElement('div');
    placeholderOverlay.className = 'placeholder-overlay';
    const overlayId = `overlay-${Date.now()}`;
    placeholderOverlay.id = overlayId;

    const wrapper = document.createElement('div');
    wrapper.className = 'placeholder-wrapper';
    wrapper.addEventListener('click', (e) => {
        e.stopPropagation();
    });

    const closeButton = document.createElement('button');
    closeButton.className = 'placeholder-close-btn';
    closeButton.innerHTML = '&times;';
    closeButton.addEventListener('click', (e) => {
        e.stopPropagation();
        placeholderOverlay.remove();
        placeholderOverlay = null;
    });

    const contentContainer = document.createElement('div');
    contentContainer.className = 'placeholder-content';

    wrapper.append(closeButton, contentContainer);
    placeholderOverlay.appendChild(wrapper);

    placeholderOverlay.style.cssText = `
        position: absolute;
        inset: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: rgba(0, 0, 0, 0.5);
        z-index: 9999;
        pointer-events: none;
    `;

    wrapper.style.cssText = `
        position: relative;
        padding: 2rem;
        border-radius: 0.5rem;
        text-align: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        background-color: rgba(0, 0, 0, 0.8);
        max-width: 80%;
        max-height: 80%;
        overflow: auto;
        pointer-events: auto;
    `;

    contentContainer.style.cssText = `
        color: white;
        font-size: 1rem;
        line-height: 1.5;
        word-wrap: break-word;
        overflow-wrap: break-word;
    `;

    closeButton.style.cssText = `
        position: absolute;
        top: 0.5rem;
        right: 0.5rem;
        width: 2rem;
        height: 2rem;
        border: none;
        background-color: transparent;
        color: white;
        font-size: 1.5rem;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0;
        border-radius: 50%;
        transition: background-color 0.3s;
        pointer-events: auto;
    `;

    closeButton.addEventListener('mouseover', () => {
        closeButton.style.backgroundColor = 'rgba(255, 255, 255, 0.2)';
    });

    closeButton.addEventListener('mouseout', () => {
        closeButton.style.backgroundColor = 'transparent';
    });

    let parentElement = parent;
    if (!parentElement) {
        parentElement = tracker?.currentMainVideoWrapper || document.body;
        while (parentElement && parentElement.offsetHeight <= 0) {
            parentElement = parentElement.parentElement;
        }
    }
    if (parentElement) {
        parentElement.appendChild(placeholderOverlay);
    }

    if (content) {
        if (typeof content === 'string') {
            contentContainer.textContent = content;
        } else {
            contentContainer.appendChild(content);
        }
        placeholderOverlay.style.display = 'flex';
    } else {
        placeholderOverlay.style.display = 'none';
    }

    placeholderOverlay.style.zIndex = '2147483647';

    if (duration > 0) {
        setTimeout(() => {
            if (placeholderOverlay && placeholderOverlay.id === overlayId) {
                placeholderOverlay.remove();
                placeholderOverlay = null;
            }
        }, duration);
    }

    return true;
};

const removeVideoPlaceholderOverlay = () => {
    const overlay = document.querySelector('.placeholder-overlay');
    if (overlay) {
        overlay.remove();
    }
};

async function createOverlayErrorMessage(error) {

    const hostname = location.hostname;

    const errorContainer = document.createElement('div');
    errorContainer.style.cssText = `
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        color: white;
        font-size: 16px;
        font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,Noto Sans,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol,Noto Color Emoji;
    `;

    const errorMessage = document.createElement('p');
    errorMessage.textContent = error.message || 'An error occurred';
    errorMessage.style.cssText = `
        font-size: 16px;
        font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,Noto Sans,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol,Noto Color Emoji;
    `;

    if (error.message && error.message.includes('Document PiP requires user activation')) {


        errorMessage.textContent = chrome.i18n.getMessage('pip_user_activation_required');

        const retryButton = document.createElement('button');
        retryButton.textContent = chrome.i18n.getMessage('retry_action');
        retryButton.style.cssText = `
            margin-top: 10px;
            padding: 8px 16px;
            background-color: #4CAF50;
            color: white;
            border: none;
            border-radius: 4px;
            cursor: pointer;
            font-size: 14px;
            font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,Noto Sans,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol,Noto Color Emoji;
        `;
        retryButton.addEventListener('click', () => {
            enterDocumentPip(true);
        });
        errorContainer.appendChild(retryButton);
    }
    if (error.message && error.message.includes('only allowed from a top-level browsing context')) {
        const newTabUrl = getNewTabUrl();
        if (newTabUrl) {
            const siteResponse = await sendRuntimeMessage('get_site_configs', { url: newTabUrl })
            const tabSiteConfigs = siteResponse.data?.siteConfigs;
            if (tabSiteConfigs?.pipRestrictionAction === 'use_standard_pip') {
                activeVideoEnterPictureInPicture();
                return null;
            } else if (tabSiteConfigs?.pipRestrictionAction === 'open_in_new_tab') {
                pauseVideo(tracker.currentMainVideo);
                await sendRuntimeMessage('add_new_tab', { url: newTabUrl, enterPictureInPicture: true });
                return null;
            }
        }
        errorMessage.textContent = chrome.i18n.getMessage('pip_window_restriction');

        const createButton = (text, bgColor, onClick) => {
            const button = document.createElement('button');
            button.textContent = text;
            button.style.cssText = `
                margin-top: 10px;
                padding: 8px 16px;
                background-color: ${bgColor};
                color: white;
                border: none;
                border-radius: 4px;
                cursor: pointer;
                font-size: 14px;
                font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica Neue,Arial,Noto Sans,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol,Noto Color Emoji;
            `;
            button.addEventListener('click', onClick);
            return button;
        };

        const buttonContainer = document.createElement('div');
        buttonContainer.style.cssText = `
            display: flex;
            flex-direction: column;
            align-items: center;
            gap: 10px;
            margin-top: 10px;
        `;

        const actionButtonsContainer = document.createElement('div');
        actionButtonsContainer.style.cssText = `
            display: flex;
            justify-content: center;
            gap: 10px;
        `;

        const standardPipButton = createButton(
            chrome.i18n.getMessage('use_standard_pip'),
            '#4CAF50',
            () => {
                playVideo(tracker.currentMainVideo);
                activeVideoEnterPictureInPicture();
                if (rememberChoiceCheckbox.checked) {
                    // Save the choice
                    sendRuntimeMessage("update_site_config", { hostname, config: { pipRestrictionAction: 'use_standard_pip' } })
                }
            }
        );
        actionButtonsContainer.appendChild(standardPipButton);


        if (newTabUrl) {
            const newTabButton = createButton(
                chrome.i18n.getMessage('open_in_new_tab'),
                '#008CBA',
                () => {
                    pauseVideo(tracker.currentMainVideo);
                    sendRuntimeMessage('add_new_tab', { url: newTabUrl, enterPictureInPicture: true });
                    if (rememberChoiceCheckbox.checked) {
                        // Save the choice
                        sendRuntimeMessage("update_site_config", { hostname, config: { pipRestrictionAction: 'open_in_new_tab' } })

                    }
                }
            );
            actionButtonsContainer.appendChild(newTabButton);
        }

        buttonContainer.appendChild(actionButtonsContainer);

        const rememberChoiceContainer = document.createElement('div');
        rememberChoiceContainer.style.cssText = `
            display: flex;
            align-items: center;
            margin-top: 10px;
        `;

        const rememberChoiceCheckbox = document.createElement('input');
        rememberChoiceCheckbox.type = 'checkbox';
        rememberChoiceCheckbox.id = 'rememberChoice';
        rememberChoiceCheckbox.checked = false;
        rememberChoiceCheckbox.style.marginRight = '5px';

        const rememberChoiceLabel = document.createElement('label');
        rememberChoiceLabel.htmlFor = 'rememberChoice';
        rememberChoiceLabel.textContent = chrome.i18n.getMessage('remember_site_pip_restriction_choice');
        rememberChoiceLabel.style.fontSize = '12px';

        rememberChoiceContainer.appendChild(rememberChoiceCheckbox);
        rememberChoiceContainer.appendChild(rememberChoiceLabel);

        buttonContainer.appendChild(rememberChoiceContainer);

        errorContainer.appendChild(buttonContainer);
    }

    function getNewTabUrl() {
        if (window !== window.top) {
            return appendAutoplayParam(window.location.href);
        }

        const iframes = Array.from(document.getElementsByTagName('iframe'));
        if (iframes.length === 0) {
            return null;
        }

        const videoIframe = iframes.find(iframe => {
            try {
                return iframe.contentDocument?.querySelector('video');
            } catch (error) {
                console.warn('Unable to access iframe content:', error);
                return false;
            }
        });

        if (videoIframe) {
            return appendAutoplayParam(videoIframe.src);
        }

        const largestIframe = iframes.reduce((prev, current) =>
            (prev.clientWidth * prev.clientHeight > current.clientWidth * current.clientHeight) ? prev : current
        );

        return appendAutoplayParam(largestIframe.src);
    }

    function appendAutoplayParam(url) {
        if (url.includes('youtube.com')) {
            const urlObj = new URL(url);
            urlObj.searchParams.set('autoplay', '1');
            return urlObj.toString();
        }
        return url;
    }

    errorContainer.appendChild(errorMessage);

    return errorContainer;
}



const detectCaption = () => {
    if (!settings?.subtitleDetection) {
        return false;
    }

    const captionKeywords = ['text-track-wrapper', 'text-track', 'player-timedtext', 'caption', 'subtitle'];
    const videoElements = document.querySelectorAll('video,smp-toucan-player');
    const ignoredTags = new Set(['A', 'IMG', 'SVG', 'TITLE', 'BUTTON', 'FOOTER', 'NAV', 'ASIDE', 'LINK', 'CODE', 'BLOCKQUOTE', 'PRE', 'SCRIPT', 'STYLE', 'TEXTAREA', 'G', 'NOSCRIPT', 'BASE', 'KBD', 'WBR', 'RT', 'RP', 'META', 'MATH', 'TTS-SENTENCE', 'AIO-CODE', 'RELIN-TARGET']);

    for (const keyword of captionKeywords) {
        for (const video of videoElements) {
            let result = null;

            // Check video element's children
            result = checkElementAndChildren(video, [keyword], ignoredTags, video);
            if (result) return result;

            let currentElement = video;

            while (currentElement) {
                // Check sibling elements
                result = checkSiblings(currentElement, [keyword], ignoredTags, video);
                if (result) return result;

                currentElement = currentElement.parentElement;
            }
        }
    }

    return null;
};

const checkElementAndChildren = (element, keywords, ignoredTags, videoElement) => {
    if (ignoredTags.has(element.tagName.toUpperCase())) return null;

    const result = checkElement(element, keywords, videoElement);
    if (result) return result;

    for (const child of element.children) {
        const childResult = checkElementAndChildren(child, keywords, ignoredTags, videoElement);
        if (childResult) return childResult;
    }

    return null;
};

const checkSiblings = (element, keywords, ignoredTags, videoElement) => {
    let sibling = element.nextElementSibling;
    while (sibling) {
        if (!ignoredTags.has(sibling.tagName.toUpperCase())) {
            const result = checkElementAndChildren(sibling, keywords, ignoredTags, videoElement);
            if (result) return result;
        }
        sibling = sibling.nextElementSibling;
    }

    sibling = element.previousElementSibling;
    while (sibling) {
        if (!ignoredTags.has(sibling.tagName.toUpperCase())) {
            const result = checkElementAndChildren(sibling, keywords, ignoredTags, videoElement);
            if (result) return result;
        }
        sibling = sibling.previousElementSibling;
    }

    return null;
};

const checkElement = (element, keywords, videoElement) => {
    try {
        const { id, className, dataset } = element;

        // Early return if element contains img or svg
        if (element.querySelector('img, svg')) return null;

        const lowercaseKeywords = keywords.map(keyword => keyword.toLowerCase());

        // Check id
        if (id && lowercaseKeywords.some(keyword => id.toLowerCase().includes(keyword))) {
            return buildCaptionResult(element, id, null, null, videoElement);
        }

        // Check dataset
        const datasetValues = Object.values(dataset);
        if (datasetValues.length > 0 && lowercaseKeywords.some(keyword =>
            datasetValues.some(value => typeof value === 'string' && value.toLowerCase().includes(keyword))
        )) {
            return buildCaptionResult(element, null, null, dataset, videoElement);
        }

        // Check className
        if (className && lowercaseKeywords.some(keyword => className.toLowerCase().includes(keyword))) {
            return buildCaptionResult(element, null, className, null, videoElement);
        }

        return null;
    } catch (error) {
        console.error("checkElement error", { element, error });
        return null;
    }
};

const buildCaptionResult = (element, id, className, dataset, videoElement) => {
    const selector = buildSelector(id, className, dataset);
    const parentSelector = buildSelector(element.parentElement.id, element.parentElement.className, element.parentElement.dataset);
    const videoParentSelector = buildSelector(videoElement.parentElement.id, videoElement.parentElement.className, videoElement.parentElement.dataset);
    let deepestElement = element;
    while (deepestElement.children.length > 0) {
        deepestElement = deepestElement.children[0];
    }
    const computedStyle = window.getComputedStyle(deepestElement);
    const style = {
        display: computedStyle.display,
        textAlign: computedStyle.textAlign,
        padding: computedStyle.padding,
        borderRadius: computedStyle.borderRadius,
        direction: computedStyle.direction,
        opacity: computedStyle.opacity,
        color: computedStyle.color,
        background: computedStyle.background,
        backgroundColor: computedStyle.backgroundColor
    };

    const captionRect = element.getBoundingClientRect();
    const videoRect = videoElement.getBoundingClientRect();

    // Check if caption and video rectangles intersect
    const intersects = !(captionRect.right < videoRect.left ||
        captionRect.left > videoRect.right ||
        captionRect.bottom < videoRect.top ||
        captionRect.top > videoRect.bottom);

    if (!intersects) {
        return null;
    }

    console.log('Found caption/subtitle element:', selector);
    console.log('Found caption/subtitle parent element:', parentSelector);
    console.log('Found video parent element:', videoParentSelector);
    return {
        caption: selector,
        captionParent: parentSelector,
        videoParent: videoParentSelector,
        captionStyle: style,
        captionRect,
        videoRect
    };
};

const buildSelector = (id, className, dataset) => {
    const selectorParts = [];

    if (id && /^[a-zA-Z_][a-zA-Z0-9_-]*$/.test(id)) {
        selectorParts.push(`#${CSS.escape(id)}`);
    }

    if (dataset) {
        for (const [key, value] of Object.entries(dataset)) {
            if (value && /^[a-zA-Z_][a-zA-Z0-9_-]*$/.test(key)) {
                selectorParts.push(`[data-${CSS.escape(key)}="${CSS.escape(value)}"]`);
            }
        }
    }

    if (typeof className === 'string') {
        const classes = className.split(' ')
            .filter(cls => /^[a-zA-Z_][a-zA-Z0-9_-]*$/.test(cls))
            .map(cls => `.${CSS.escape(cls)}`)
            .join('');
        if (classes) {
            selectorParts.push(classes);
        }
    }

    return selectorParts.join('');
};




