import { debounce } from "lodash-es";;
import { frameId, settings } from "./runtime";
import { sendRuntimeMessage } from "../../core/browers/runtime";
import { mainVideo } from "./video";


let scrollElement = null;

export function calculateScrollPercentage(element = document.body) {
    try {
        if (element === document) {
            element = document.documentElement;
        }

        if (!(element instanceof HTMLElement)) {
            throw new Error("Invalid element provided");
        }
        scrollElement = element;
        const isDocumentElement = (element === document.documentElement || element === document.body);
        const scrollTop = isDocumentElement ? window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop : element.scrollTop;
        const scrollHeight = isDocumentElement ? Math.max(document.documentElement.scrollHeight, document.body.scrollHeight) : element.scrollHeight;
        const clientHeight = isDocumentElement ? window.innerHeight : element.clientHeight;
        if (scrollHeight <= clientHeight) {
            return 0.0;
        }
        // console.log("height", { scrollTop, scrollHeight, clientHeight, bl: scrollHeight / clientHeight })
        if (scrollHeight / clientHeight < (settings?.scrollHeightRatio || 2)) {
            return 0.0;
        }

        const scrollPercentage = (scrollTop / (scrollHeight - clientHeight)) * 100;

        return parseFloat(Math.min(Math.max(scrollPercentage, 0), 100).toFixed(1));
    } catch (error) {
        console.error(error);
        return 0.0;
    }
}


const handleScrolled = debounce(async (event) => {

    await sendPageScrollInfo(event.target);
}, 100)

export const pageScrolledObserver = async () => {
    if (frameId === 0) {
        await sendPageScrollInfo();
    }
    window.removeEventListener('scroll', handleScrolled);
    window.addEventListener('scroll', handleScrolled, true);

}

async function sendPageScrollInfo(element) {
    try {
        if (settings?.readingProgressBar && mainVideo === null) {

            const scrollProgress = calculateScrollPercentage(element);
            await sendRuntimeMessage("page_scrolled", {
                scrollProgress: scrollProgress
            })
        } else {
            await sendRuntimeMessage("page_scrolled", {
                scrollProgress: 0
            })
        }
    } catch (error) {
        console.error("sendPageScrollInfo error", error)
    }

}

export const scrollToTop = () => {
    console.log("scrollToTop", scrollElement)
    scrollElement?.scrollTo({ top: 0, left: 0, behavior: "smooth", });
}

export const scrollToBottom = () => {
    console.log("scrollToBottom", scrollElement)
    scrollElement?.scrollTo({ top: scrollElement.scrollHeight, left: 0, behavior: "smooth", });
}

export const displayInfo = () => {
    const {
        screen: { height, width },
        innerHeight,
        innerWidth,
        devicePixelRatio,
    } = window;

    const displayInfo = {
        height: height * devicePixelRatio,
        width: width * devicePixelRatio,
        tabHeight: innerHeight,
        tabWidth: innerWidth,
    };

    return displayInfo
}