import { frameId, siteConfigs } from "./runtime"

export const updatePageBySiteConfigs = () => {
    if (frameId === 0) {
        if (siteConfigs.linkOpenOptions?.length > 0) {
            updatePageLinks(siteConfigs.linkOpenOptions);
        } else {
            resetPageLinks();
        }
        if (siteConfigs.titleOptions?.length > 0) {
            updatePageTitle(siteConfigs.titleOptions);
        }

        if (siteConfigs.linkRefreshOptions?.length > 0) {
            if (document.readyState === 'loading') {
                document.addEventListener('DOMContentLoaded', () => {
                    linkRefresh(siteConfigs.linkRefreshOptions);
                });
            } else {
                linkRefresh(siteConfigs.linkRefreshOptions);
            }
        }
    } else {
        resetPageLinks();
    }
}

const updatePageLinks = (linkOpenOptions) => {
    document.querySelectorAll("a").forEach(a => {
        if (a.href === window.location.href) return;

        const matchedOption = linkOpenOptions.find(option => {
            try {
                return new RegExp(option.regex).test(a.href);
            } catch {
                return a.href.includes(option.regex);
            }
        });

        if (matchedOption) {
            a.setAttribute("link_target_backup", a.getAttribute("target") || "");
            a.setAttribute("target", matchedOption.target);
        }
    });
}

const updatePageTitle = (titleOptions) => {
    const matchedOption = titleOptions.find(option => {
        if (option.matchPattern === 'exact') {
            return option.url === document.location.href;
        } else if (option.matchPattern === 'contains') {
            return window.location.href.includes(option.url) && window.location.href.includes('?');
        }
        return false;
    });
    if (matchedOption && matchedOption.updatePageTitle) {
        document.title = matchedOption.newTitle;
    }
}

const resetPageLinks = () => {
    document.querySelectorAll("a[link_target_backup]").forEach(a => {
        a.setAttribute("target", a.getAttribute("link_target_backup"));
        a.removeAttribute("link_target_backup");
    });
}

let pageRefreshInterval;
let logInterval;

const linkRefresh = (linkRefreshOptions) => {
    const matchedOption = linkRefreshOptions.find(option => {
        try {
            return new RegExp(option.regex).test(window.location.href);
        } catch {
            return window.location.href.includes(option.regex);
        }
    });

    console.debug("linkRefresh matchedOption ", matchedOption);

    if (!matchedOption || matchedOption.refreshInterval <= 0) return;

    clearInterval(pageRefreshInterval);
    clearInterval(logInterval);

    let existingFloatBall = document.getElementById('refresh-float-ball');
    if (existingFloatBall) {
        document.body.removeChild(existingFloatBall);
    }

    const floatBall = createFloatBall();
    const deleteButton = createDeleteButton(floatBall);
    const timerSpan = document.createElement('span');

    floatBall.appendChild(timerSpan);
    floatBall.appendChild(deleteButton);
    window.postMessage({ action: "page_keep_alive" }, location.origin);
    let t = 0;
    const refreshInterval = matchedOption.refreshInterval * 60 * 1000;
    logInterval = setInterval(() => {
        const remainingTime = (refreshInterval - t) / 1000;
        timerSpan.innerText = `${Math.max(0, Math.floor(remainingTime))}`;
        console.log("refresh wait:", { wait: remainingTime.toFixed(1) + " s", link: location.href, frameId });
        t += 1000;
        if (t >= refreshInterval) {
            clearInterval(logInterval);
            window.location.reload();
        }
    }, 1000);

    document.body.appendChild(floatBall);
    enableDrag(floatBall);
}

const createFloatBall = () => {
    const floatBall = document.createElement('div');
    floatBall.id = 'refresh-float-ball';
    Object.assign(floatBall.style, {
        position: 'fixed',
        bottom: '40px',
        right: '40px',
        width: '40px',
        height: '40px',
        borderRadius: '50%',
        backgroundColor: 'rgba(0, 0, 0, 0.7)',
        color: 'white',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        cursor: 'move',
        zIndex: '99999',
        boxShadow: '0 2px 5px rgba(0,0,0,0.2)',
        transition: 'all 0.3s ease',
        fontSize: '14px',
        fontWeight: 'bold'
    });
    floatBall.title = chrome.i18n.getMessage('countdown_refresh');
    floatBall.addEventListener('mouseover', () => {
        floatBall.style.transform = 'scale(1.1)';
    });
    floatBall.addEventListener('mouseout', () => {
        floatBall.style.transform = 'scale(1)';
    });
    return floatBall;
}

const createDeleteButton = (floatBall) => {
    const deleteButton = document.createElement('button');
    deleteButton.innerText = '';
    Object.assign(deleteButton.style, {
        position: 'absolute',
        top: '-8px',
        right: '-8px',
        width: '20px',
        height: '20px',
        backgroundColor: 'rgba(255, 0, 0, 0.8)',
        color: 'white',
        border: 'none',
        borderRadius: '50%',
        cursor: 'pointer',
        zIndex: '199999',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        fontSize: '14px',
        fontWeight: 'bold',
        opacity: '0',
        transition: 'opacity 0.3s ease, transform 0.3s ease',
        boxShadow: '0 1px 3px rgba(0,0,0,0.3)'
    });
    deleteButton.addEventListener('click', (e) => {
        e.stopPropagation();
        clearInterval(logInterval);
        clearInterval(pageRefreshInterval);
        floatBall.style.transform = 'scale(0)';
        setTimeout(() => document.body.removeChild(floatBall), 300);
    });
    deleteButton.addEventListener('mouseover', () => {
        deleteButton.style.transform = 'scale(1.1)';
    });
    deleteButton.addEventListener('mouseout', () => {
        deleteButton.style.transform = 'scale(1)';
    });
    floatBall.addEventListener('mouseover', () => {
        deleteButton.style.opacity = '1';
    });
    floatBall.addEventListener('mouseout', () => {
        deleteButton.style.opacity = '0';
    });
    return deleteButton;
}

const enableDrag = (element) => {
    let isDragging = false;
    let startX, startY, initialLeft, initialTop;

    element.onmousedown = dragStart;

    function dragStart(e) {
        e.preventDefault();
        isDragging = true;
        startX = e.clientX;
        startY = e.clientY;
        const rect = element.getBoundingClientRect();
        initialLeft = rect.left;
        initialTop = rect.top;
        document.addEventListener('mousemove', drag);
        document.addEventListener('mouseup', dragEnd);
    }

    function drag(e) {
        if (!isDragging) return;
        const deltaX = e.clientX - startX;
        const deltaY = e.clientY - startY;
        requestAnimationFrame(() => {
            element.style.left = `${initialLeft + deltaX}px`;
            element.style.top = `${initialTop + deltaY}px`;
            element.style.right = 'auto';
            element.style.bottom = 'auto';
        });
    }

    function dragEnd() {
        isDragging = false;
        document.removeEventListener('mousemove', drag);
        document.removeEventListener('mouseup', dragEnd);
    }
}
