const IGNORED_ERRORS = [
    /Could not establish connection. Receiving end does not exist/,
    /The message port closed before a response was received/,
    /A listener indicated an asynchronous response by returning true/,
];

const shouldIgnoreError = (error) => {
    return IGNORED_ERRORS.some(pattern =>
        typeof pattern === 'string'
            ? error.includes(pattern)
            : pattern.test(error)
    );
};

export const sendRuntimeMessage = async (action, data = {}, from, target) => {
    try {
        const response = await chrome.runtime.sendMessage({ action: action, data: data, from: from, target: target });
        return response;
    } catch (error) {
        if (!shouldIgnoreError(error.message)) {
            console.error("sendRuntimeMessage error", { error, action, data, from, target });
        }
        return false;
    }
};

export const sendTabMessage = async (tabId, action, data = {}) => {
    try {
        const response = await chrome.tabs.sendMessage(tabId, { action: action, data: data });
        return response;
    } catch (error) {
        if (!shouldIgnoreError(error.message)) {
            console.error("sendTabMessage error", { tabId, action, data, error });
        }
        return false;
    }
};
