import { processContentScriptsMessage, startup } from "./runtime";

const initialize = () => {

  startup();
  chrome.runtime.onMessage.addListener((message, sender, sendResponse) => {
    console.log("chrome runtime onMessage", message, sender, sendResponse);
    processContentScriptsMessage(message, sender, sendResponse);
    return true;
  });
};

const blockUrls = [
  "googleads.g.doubleclick.net",
  "s1.hdslb.com",
  "oapi.dingtalk.com",
  "login.dingtalk.com",
  "imasdk.googleapis.com",
  "acdn.adnxs.com",
  "pos.baidu.com",
  "js-sec.indexww.com",
  "g.alicdn.com",
  "ads.pubmatic.com",
  "challenges.cloudflare.com",
  "accounts.google.com",
  "images-na.ssl-images-amazon.com",
  "tpc.googlesyndication.com",
  "js.stripe.com",
  "acdn.adnxs-simple.com",
  "s.union.360.cn",
  "s.amazon-adsystem.com",
  "www.recaptcha.net",
  "s7.addthis.com",
  "z.moatads.com",
  "https://www.marketwatch.com/static_html/daa-min.html",
  "tr.snapchat.com",
  "ct.pinterest.com",
  "*.moatads.com",
  "secure-us.imrworldwide.com",
  "static.noeyeon.click",
  "widgets.outbrain.com",
  "www.dianomi.com/smartads.epl",
  "secure-assets.rubiconproject.com",
  "eus.rubiconproject.com",
  "eus.rubiconproject.com",
  "i.liadm.com",
  "eb2.3lift.com",
  "googleads.g.doubleclick.net",
  "https://www.google.com/recaptcha/*",
  "ad.doubanio.com",
  "datawrapper.dwcdn.net",
  "*://*/*\\.svg",
  "file://*/*\\.xml",
  "file://*/*\\.svg"
];

const shouldBlock = (url) => {
  return blockUrls.some(blockedUrl => new RegExp(blockedUrl.replace(/\*/g, '.*')).test(url));
};

if (!shouldBlock(window.location.href)) {
  window.addEventListener('DOMContentLoaded', initialize);
  window.addEventListener('pageshow', (event) => {
    if (event.persisted) {
      initialize();
    } else {
      startup();
    }
  });
}