export function getAllLocalStorageItems() {
  const localStorageItems = {};
  for (let i = 0; i < localStorage.length; i++) {
    const key = localStorage.key(i);
    const value = localStorage.getItem(key);
    localStorageItems[key] = value;
  }
  return localStorageItems;
}

export function getAllSessionStorageItems() {
  const sessionStorageItems = {};
  for (let i = 0; i < sessionStorage.length; i++) {
    const key = sessionStorage.key(i);
    const value = sessionStorage.getItem(key);
    sessionStorageItems[key] = value;
  }
  return sessionStorageItems;
}

export function clearAllStorage() {
  localStorage.clear();
  sessionStorage.clear();
}

export async function clearAllData(configs) {
  if (!configs) return;
  if (!window.location.href.startsWith("http")) return;
  const clearFunctions = [];
  if (configs.cacheStorage) clearFunctions.push(clearCacheStorage());
  if (configs.fileSystem) clearFunctions.push(clearFileSystem());
  if (configs.indexedDB) clearFunctions.push(clearIndexedDB());
  if (configs.localStorage) clearFunctions.push(clearLocalStorage());
  if (configs.serviceWorkers) clearFunctions.push(clearServiceWorkers());
  if (configs.webSQL) clearFunctions.push(clearWebSQL());
  if (configs.sessionStorage) clearFunctions.push(clearSessionStorage());
  await Promise.all(clearFunctions);
}

async function clearCacheStorage() {
  if ('caches' in window) {
    const cacheNames = await caches.keys();
    console.log('Clearing Cache Storage:', cacheNames);
    await Promise.all(cacheNames.map(cacheName => caches.delete(cacheName)));
  }
}

async function clearFileSystem() {
  if ('webkitRequestFileSystem' in window) {
    console.log('Clearing File System');
    window.webkitRequestFileSystem(window.TEMPORARY, 1024 * 1024, fs => {
      const dirReader = fs.root.createReader();
      dirReader.readEntries(entries => {
        entries.forEach(entry => {
          if (entry.isDirectory) {
            console.log('Removing Directory:', entry.fullPath);
            entry.removeRecursively(() => { }, () => { });
          } else {
            console.log('Removing File:', entry.fullPath);
            entry.remove(() => { }, () => { });
          }
        });
      });
    }, error => { console.error('Error clearing file system:', error); });
  }
}

async function clearIndexedDB() {
  const indexedDBDatabases = await indexedDB.databases();
  console.log('Clearing IndexedDB Databases:', indexedDBDatabases.map(db => db.name));
  for (const db of indexedDBDatabases) {
    indexedDB.deleteDatabase(db.name);
  }
}

function clearLocalStorage() {
  console.log('Clearing Local Storage');
  localStorage.clear();
}

async function clearServiceWorkers() {
  if ('serviceWorker' in navigator) {
    const registrations = await navigator.serviceWorker.getRegistrations();
    console.log('Clearing Service Workers:', registrations);
    for (const registration of registrations) {
      registration.unregister();
    }
  }
}

async function clearWebSQL() {
  if (window.openDatabase) {
    const dbs = await new Promise(resolve => {
      const db = window.openDatabase('dummy', '1.0', 'dummy', 1);
      db.transaction(tx => {
        tx.executeSql('SELECT name FROM sqlite_master WHERE type="table"', [], (tx, results) => {
          const dbNames = [];
          for (let i = 0; i < results.rows.length; i++) {
            dbNames.push(results.rows.item(i).name);
          }
          resolve(dbNames);
        });
      });
    });

    console.log('Clearing WebSQL Databases:', dbs);
    for (const dbName of dbs) {
      const db = window.openDatabase(dbName, '1.0', dbName, 1);
      db.transaction(tx => {
        console.log('Dropping Table:', dbName);
        tx.executeSql(`DROP TABLE IF EXISTS ${dbName}`);
      });
    }
  }
}

function clearSessionStorage() {
  console.log('Clearing Session Storage');
  sessionStorage.clear();
}