import { VideoTracker } from "./VideoTracker";

let pipVideoTracker
export const getVideoTracker = () => {
    if (!pipVideoTracker) {
        pipVideoTracker = new VideoTracker();
        pipVideoTracker.init();
    }
    return pipVideoTracker
}


export function switchPictureInPicture() {
    if (document.pictureInPictureElement) {
        document.exitPictureInPicture();
        return true;
    }
    switchInPictureInPicture()
}

export function exitPictureInPicture() {
    if (document.pictureInPictureElement) {
        document.exitPictureInPicture();
        return true;
    }
    return false
}
export async function activeVideoEnterPictureInPicture() {
    if (!navigator.userActivation.isActive) {
        // return false;
    }
    const video = getVideoTracker()?.currentMainVideo;

    console.log('activeVideoEnterPictureInPicture', video);
    if ((video === null || video?.paused === true || video?.muted === true)) {
        return false
    }
    if (video) {

        if (video.hasAttribute('disablePictureInPicture')) {
            video.removeAttribute('disablePictureInPicture');
        }
        if (document.pictureInPictureElement) {
            return false;
        }
        try {
            await video.requestPictureInPicture();
        } catch (error) {
            console.error("requestPictureInPicture error", error.message);
        }
    }
    return false;
}

export async function switchInPictureInPicture() {

    const video = getVideoTracker()?.currentMainVideo;
    console.log('switchInPictureInPicture', video);
    if ((video === null)) {
        return false
    }
    if (video) {
        if (video.hasAttribute('disablePictureInPicture')) {
            video.removeAttribute('disablePictureInPicture');
        }
        try {
            await video.requestPictureInPicture();
        } catch (error) {
            console.error("requestPictureInPicture error", error.message);
        }
    }
    return false;
}






