export class VideoTracker {
    constructor() {
        if (VideoTracker.instance) {
            return VideoTracker.instance;
        }

        this.videos = new Set();
        this.videoPositions = new Map(); // Store original positions of videos
        this._currentMainVideo = null;
        this._currentMainVideoWrapper = null;
        this._currentMainVideoWrapperSelector = null;
        this._currentMainVideoInViewport = false;
        this.intersectionObserver = null;
        this.mutationObserver = null;
        this.checkTimeout = null;
        this.eventListeners = {};
        this.mainVideoEventListeners = {};
        this._initialized = false; // Add a flag to track initialization

        VideoTracker.instance = this;
    }

    init() {
        if (document.body) {
            this.initializeTracker();
        } else {
            window.addEventListener('DOMContentLoaded', () => this.initializeTracker());
        }
    }

    initializeTracker() {
        if (this._initialized) return; // Ensure initializeTracker only runs once

        this.setupMutationObserver();
        this.setupIntersectionObserver();
        this.findAllVideos();
        this.setupVideoListeners();
        window.addEventListener('scroll', this.throttle(this.checkMainVideo.bind(this), 200));
        this.checkMainVideo();
        console.log('VideoTracker initialized');

        this._initialized = true; // Set the flag to true after initialization
        this.emit('initialized'); // Emit the initialized event
    }

    findAllVideos() {
        document.querySelectorAll('video').forEach(video => this.addVideo(video));
        console.log(`Found ${this.videos.size} videos on the page`);
    }

    setupIntersectionObserver() {
        const options = {
            root: null,
            rootMargin: '0px',
            threshold: 0.5
        };

        this.intersectionObserver = new IntersectionObserver((entries) => {
            entries.forEach(entry => {
                if (entry.isIntersecting) {
                    //
                    if (entry.target === this._currentMainVideoWrapper) {
                        this._currentMainVideoInViewport = true;
                        this.emit('mainVideoIntersecting', this._currentMainVideo);
                    }
                    this.checkMainVideo();
                } else {
                    if (entry.target === this._currentMainVideo) {
                        this._currentMainVideoInViewport = false;
                        this.emit('mainVideoNotIntersecting', entry.target);
                    }
                }
            });
        }, options);
    }

    setupVideoListeners() {
        this.videos.forEach(video => this.addVideoListeners(video));
    }

    addVideoListeners(video) {
        if (!video._videoEventListeners) {
            const videoEventListeners = {
                play: () => this.checkMainVideo(),
                pause: () => this.checkMainVideo(),
                ended: () => this.handleVideoEnded(video),
                error: (e) => this.handleVideoError(video, e)
            };

            video._videoEventListeners = videoEventListeners;

            Object.keys(videoEventListeners).forEach(eventName => {
                video.addEventListener(eventName, videoEventListeners[eventName]);
            });
        }
    }

    handleVideoEnded(video) {
        console.log('Video ended:', video);
        this.emit('videoEnded', video);
        if (this.checkTimeout) {
            clearTimeout(this.checkTimeout);
        }
        this.checkTimeout = setTimeout(() => {
            console.log('Checking for new main video after previous video ended');
            this.checkMainVideo();
        }, 3000);
    }

    handleVideoError(video, error) {
        console.error('Video error:', video, error);
        this.emit('videoError', { video, error });
    }

    setupMutationObserver() {
        this.mutationObserver = new MutationObserver((mutations) => {
            let needsCheck = false;

            mutations.forEach(mutation => {
                if (mutation.type === 'childList') {
                    mutation.addedNodes.forEach(node => {
                        if (node.nodeName === 'VIDEO') {
                            this.addVideo(node);
                            needsCheck = true;
                        } else if (node.nodeType === Node.ELEMENT_NODE) {
                            node.querySelectorAll('video').forEach(video => {
                                this.addVideo(video);
                                needsCheck = true;
                            });
                        }
                    });

                    mutation.removedNodes.forEach(node => {
                        if (node.nodeName === 'VIDEO') {
                            this.removeVideo(node);
                            needsCheck = true;
                        } else if (node.nodeType === Node.ELEMENT_NODE) {
                            node.querySelectorAll('video').forEach(video => {
                                this.removeVideo(video);
                                needsCheck = true;
                            });
                        }
                    });
                } else if (mutation.type === 'attributes') {
                    if (mutation.target.nodeName === 'VIDEO' && mutation.attributeName === 'src') {
                        this.handleVideoSourceChange(mutation.target);
                        needsCheck = true;
                    }
                }
            });

            if (needsCheck) {
                this.checkMainVideo();
            }
        });

        if (document.body) {
            this.mutationObserver.observe(document.body, {
                childList: true,
                subtree: true,
                attributes: true,
                attributeFilter: ['src']
            });
        } else {
            console.error('document.body is not available for MutationObserver');
        }
    }

    handleVideoSourceChange(video) {
        console.log('Video source changed:', video);
        if (!video.src) this.videos.delete(video);
        if (video.src) this.addVideo(video);
        this.checkMainVideo();
        this.emit('videoSourceChanged', video);
    }

    addVideo(video) {
        if ((video.duration && video.duration <= 5)) {
            return;
        }
        if (!this.videos.has(video)) {
            this.videos.add(video);

            this.addVideoListeners(video);
            this.saveVideoPosition(video); // Save the original position of the video
            console.log('New video added:', video);
            this.emit('videoAdded', video);
        }
    }

    removeVideo(video) {
        if (this.videos.has(video)) {
            this.videos.delete(video);


            if (video._videoEventListeners) {
                Object.keys(video._videoEventListeners).forEach(eventName => {
                    video.removeEventListener(eventName, video._videoEventListeners[eventName]);
                });
                delete video._videoEventListeners;
            }

            console.log('Video removed:', video);
            this.emit('videoRemoved', video);
            if (this._currentMainVideo === video) {
                this.intersectionObserver.unobserve(video);
                this._currentMainVideo = null;
                this._currentMainVideoInViewport = false;
                this.checkMainVideo();
            }
        }
    }

    saveVideoPosition(video) {
        const parent = video.parentElement;
        const index = Array.from(parent.children).indexOf(video);
        this.videoPositions.set(video, { parent, index });
    }

    restoreVideoPosition(video) {
        const position = this.videoPositions.get(video);
        if (position) {
            const { parent, index } = position;
            const children = Array.from(parent.children);
            if (index >= children.length) {
                parent.appendChild(video);
            } else {
                parent.insertBefore(video, children[index]);
            }
            console.log('Video restored to original position:', video);
        } else {
            console.error('No saved position for video:', video);
        }
    }

    checkMainVideo() {
        let maxVisibleArea = 0;
        let mainVideo = null;

        for (const video of this.videos) {
            if (video.paused || video.ended) continue;
            const visibleArea = this.getVisibleArea(video.getBoundingClientRect());
            if (visibleArea > maxVisibleArea) {
                maxVisibleArea = visibleArea;
                mainVideo = video;
            }
        }

        if (mainVideo && mainVideo !== this._currentMainVideo) {
            const previousMainVideo = this._currentMainVideo;
            this._currentMainVideo = mainVideo;
            this._currentMainVideoWrapper = this.findVideoWrapper(mainVideo);
            this._currentMainVideoWrapperSelector = this.findVideoWrapperSelector(mainVideo);
            this._currentMainVideoInViewport = this.isInViewport(mainVideo);
            this.intersectionObserver.observe(this._currentMainVideo);
            this.intersectionObserver.observe(this._currentMainVideoWrapper);
            console.log('Main video changed:', this._currentMainVideo);
            this.emit('mainVideoChanged', { previous: previousMainVideo, current: mainVideo });
            this.setupMainVideoListeners(mainVideo);
        } else if (!mainVideo) {
            this._currentMainVideoInViewport = false;
        }
    }

    findVideoWrapper(video) {
        // Always return the immediate parent element
        return video.parentElement;
    }
    findVideoWrapperSelector(video) {
        const parent = video.parentElement;
        if (!parent) return null;
        // Generate a unique selector for the parent element
        let selector = '';
        // Check for ID
        if (parent.id) {
            selector = `#${CSS.escape(parent.id)}`;
        } else {
            // Use tag name and classes
            selector = parent.tagName.toLowerCase();
            if (parent.className) {
                const classes = parent.className.split(' ').filter(c => c.trim() !== '');
                selector += classes.map(c => `.${CSS.escape(c)}`).join('');
            }

            // Add nth-child if needed
            const siblings = Array.from(parent.parentNode.children);
            const index = siblings.indexOf(parent) + 1;
            if (index > 1 || siblings.length > 1) {
                selector += `:nth-child(${index})`;
            }
        }

        // Add data attributes if present
        for (const attr of parent.attributes) {
            if (attr.name.startsWith('data-')) {
                selector += `[${attr.name}="${CSS.escape(attr.value)}"]`;
            }
        }

        return selector;
    }

    setupMainVideoListeners(video) {
        if (video) {
            // Remove existing listeners to prevent duplicate event triggers
            this.removeMainVideoListeners(video);

            const emitMainVideoUpdated = (eventName, video) => this.emit('mainVideoUpdated', eventName, video);
            this.mainVideoEventListeners = {
                play: () => {
                    this.emit('mainVideoPlay', video);
                    emitMainVideoUpdated('mainVideoPlay', video);
                },
                pause: () => {
                    this.emit('mainVideoPause', video);
                    emitMainVideoUpdated('mainVideoPause', video);
                },
                ended: () => {
                    this.emit('mainVideoEnded', video);
                    emitMainVideoUpdated('mainVideoEnded', video);
                },
                timeupdate: () => {
                    this.emit('mainVideoTimeUpdate', video);
                    emitMainVideoUpdated('mainVideoTimeUpdate', video);
                },
                volumechange: () => {
                    this.emit('mainVideoVolumeChange', video);
                    emitMainVideoUpdated('mainVideoVolumeChange', video);
                },
                ratechange: () => {
                    this.emit('mainVideoRateChange', video);
                    emitMainVideoUpdated('mainVideoRateChange', video);
                },
                seeked: () => {
                    this.emit('mainVideoSeeked', video);
                    emitMainVideoUpdated('mainVideoSeeked', video);
                },
                seeking: () => {
                    this.emit('mainVideoSeeking', video);
                    emitMainVideoUpdated('mainVideoSeeking', video);
                },
                stalled: () => {
                    this.emit('mainVideoStalled', video);
                    emitMainVideoUpdated('mainVideoStalled', video);
                },
                suspend: () => {
                    this.emit('mainVideoSuspend', video);
                    emitMainVideoUpdated('mainVideoSuspend', video);
                },
                progress: () => {
                    const buffered = video.buffered;
                    const duration = video.duration;
                    if (buffered.length) {
                        const bufferedEnd = buffered.end(buffered.length - 1);
                        const percent = (bufferedEnd / duration) * 100;
                        this.emit('mainVideoProgress', { video, percent, readyState: video.readyState });
                    }
                }
            };

            Object.keys(this.mainVideoEventListeners).forEach(eventName => {
                video.addEventListener(eventName, this.mainVideoEventListeners[eventName]);
            });
        }
    }

    removeMainVideoListeners(video) {
        if (video && this.mainVideoEventListeners) {
            Object.keys(this.mainVideoEventListeners).forEach(eventName => {
                video.removeEventListener(eventName, this.mainVideoEventListeners[eventName]);
            });
        }
    }

    getVisibleArea(rect) {
        const windowHeight = window.innerHeight;
        const top = Math.max(rect.top, 0);
        const bottom = Math.min(rect.bottom, windowHeight);
        return Math.max(0, bottom - top) * rect.width;
    }

    isInViewport(video) {
        const rect = video.getBoundingClientRect();
        return rect.top >= 0 && rect.left >= 0 && rect.bottom <= (window.innerHeight || document.documentElement.clientHeight) && rect.right <= (window.innerWidth || document.documentElement.clientWidth);
    }
    // Custom event system
    on(eventName, callback) {
        if (!this.eventListeners[eventName]) {
            this.eventListeners[eventName] = [];
        }
        this.eventListeners[eventName].push(callback);
    }

    off(eventName, callback) {
        if (this.eventListeners[eventName]) {
            this.eventListeners[eventName] = this.eventListeners[eventName].filter(
                listener => listener !== callback
            );
        }
    }

    emit(eventName, ...data) {
        if (this.eventListeners[eventName]) {
            this.eventListeners[eventName].forEach(callback => callback(...data));
        }
    }

    // Utility function for throttling
    throttle(func, limit) {
        let inThrottle;
        return function () {
            const args = arguments;
            const context = this;
            if (!inThrottle) {
                func.apply(context, args);
                inThrottle = true;
                setTimeout(() => inThrottle = false, limit);
            }
        }
    }

    // Getter for currentMainVideo
    get currentMainVideo() {
        return this._currentMainVideo;
    }

    set currentMainVideo(video) {
        const previousVideo = this._currentMainVideo;
        this._currentMainVideo = video;
        this.emit('mainVideoChanged', { previous: previousVideo, current: video });
    }
    get currentMainVideoWrapper() {
        return this._currentMainVideoWrapper;
    }
    get currentMainVideoWrapperSelector() {
        return this._currentMainVideoWrapperSelector;
    }
    get currentMainVideoInViewport() {
        return this._currentMainVideoInViewport;
    }
}


