import { settings } from "./runtime";
import { sendRuntimeMessage } from "../../core/browers/runtime";
import { debounce } from "lodash-es";

const createButton = (titleKey, innerHTML, clickHandler) => {
    const button = document.createElement('div');
    button.innerHTML = innerHTML;
    button.title = chrome.i18n.getMessage(titleKey);
    Object.assign(button.style, {
        cursor: 'pointer',
        fontSize: '14px',
        color: settings.theme === 'dark' ? '#ccc' : '#666',
        width: '18px',
        height: '18px',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        borderRadius: '0',
        backgroundColor: 'transparent',
        transition: 'color 0.3s ease, transform 0.3s ease'
    });

    button.addEventListener('mouseenter', () => {
        button.style.color = settings.theme === 'dark' ? '#ff6666' : '#ff9999';
        button.style.transform = 'scale(1.1)';
    });

    button.addEventListener('mouseleave', () => {
        button.style.color = settings.theme === 'dark' ? '#ccc' : '#666';
        button.style.transform = 'scale(1)';
    });

    button.addEventListener('click', clickHandler);

    return button;
};

const createSidebar = () => {
    if (!settings.sidebarEnabled || document.getElementById('customSidebar')) return;

    const isLeftSided = settings.sidebarPosition === 'left';
    const sidebarHtml = document.createElement('div');
    sidebarHtml.id = 'customSidebar';

    const header = document.createElement('div');
    header.id = 'sidebarHeader';
    header.innerHTML = `<span>${chrome.i18n.getMessage('appShortName')}</span>`;
    Object.assign(header.style, {
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        padding: '10px',
        backgroundColor: 'transparent',
        color: settings.theme === 'dark' ? '#fff' : '#000',
        boxShadow: '0 2px 5px rgba(0,0,0,0.1)',
        zIndex: '10000',
        cursor: 'pointer'
    });

    const buttonContainer = document.createElement('div');
    Object.assign(buttonContainer.style, {
        display: 'flex',
        alignItems: 'center',
        gap: '10px'
    });

    buttonContainer.appendChild(createButton('disableSidebar', '<svg xmlns="http://www.w3.org/2000/svg" height="18px" viewBox="0 -960 960 960" width="18px" fill="currentColor"><path d="M480-80q-83 0-156-31.5T197-197q-54-54-85.5-127T80-480q0-83 31.5-156T197-763q54-54 127-85.5T480-880q83 0 156 31.5T763-763q54 54 85.5 127T880-480q0 83-31.5 156T763-197q-54 54-127 85.5T480-80Zm0-80q54 0 104-17.5t92-50.5L228-676q-33 42-50.5 92T160-480q0 134 93 227t227 93Zm252-124q33-42 50.5-92T800-480q0-134-93-227t-227-93q-54 0-104 17.5T284-732l448 448Z"/></svg>', () => confirm(chrome.i18n.getMessage('disableSidebarAlert')) && sendRuntimeMessage('disable_sidebar')));
    buttonContainer.appendChild(createButton('toggleSidebarPosition', isLeftSided ? '<svg xmlns="http://www.w3.org/2000/svg" height="18px" viewBox="0 -960 960 960" width="18px" fill="currentColor"><path d="M800-240v-480h80v480h-80Zm-320 0-57-56 144-144H80v-80h487L424-664l56-56 240 240-240 240Z"/></svg>' : '<svg xmlns="http://www.w3.org/2000/svg" height="18px" viewBox="0 -960 960 960" width="18px" fill="currentColor"><path d="M160-240H80v-480h80v480Zm320 0L240-480l240-240 56 56-143 144h487v80H393l144 144-57 56Z"/></svg>', () => sendRuntimeMessage('update_sidebar_position', { position: isLeftSided ? 'right' : 'left' })));
    buttonContainer.appendChild(createButton('closeSidebar', '<svg xmlns="http://www.w3.org/2000/svg" height="18px" viewBox="0 -960 960 960" width="18px" fill="currentColor"><path d="m256-200-56-56 224-224-224-224 56-56 224 224 224-224 56 56-224 224 224 224-56 56-224-224-224 224Z"/></svg>', () => removeSidebar()));

    header.appendChild(buttonContainer);
    sidebarHtml.appendChild(header);

    const iframe = document.createElement('iframe');
    iframe.src = chrome.runtime.getURL('sidebar.html');
    iframe.setAttribute('tabindex', '-1');
    Object.assign(iframe.style, {
        height: 'calc(100% - 50px)',
        width: '100%',
        border: 'none',
        overflowY: 'hidden'
    });
    sidebarHtml.appendChild(iframe);
    iframe.onload = () => {
        if (iframe.contentWindow) {
            iframe.contentWindow.postMessage({ type: 'SIDEBAR_LOADED' }, '*');
        }
        header.addEventListener('click', () => {
            if (iframe.contentWindow) {
                iframe.contentWindow.postMessage({ type: 'SIDEBAR_LOADED' }, '*');
            }
        });
    };

    Object.assign(sidebarHtml.style, {
        position: 'fixed',
        top: '0',
        [isLeftSided ? 'left' : 'right']: '0',
        height: '100%',
        zIndex: '99999',
        backgroundColor: settings.theme === 'dark' ? '#222' : 'rgba(255,255,255,0.75)',
        backdropFilter: "blur(15px)",
        color: settings.theme === 'dark' ? '#fff' : '#000',
        boxShadow: isLeftSided ? '2px 0 5px rgba(0,0,0,0.1)' : '-2px 0 5px rgba(0,0,0,0.1)',
        transition: 'transform 0.3s ease, width 0.3s ease',
        width: settings.sidebarWidth || '300px',
        transform: `translateX(${isLeftSided ? '-100%' : '100%'})`,
    });

    const resizeHandle = document.createElement('div');
    Object.assign(resizeHandle.style, {
        position: 'absolute',
        top: '0',
        [isLeftSided ? 'right' : 'left']: '-5px',
        width: '10px',
        height: '100%',
        cursor: 'ew-resize',
        backgroundColor: 'transparent',
        transition: 'background-color 0.3s ease'
    });
    sidebarHtml.appendChild(resizeHandle);

    let startX, startWidth, isMouseOverSidebar = false, isMouseDown = false;

    const showSidebar = () => {
        sidebarHtml.style.transform = 'translateX(0)';
        if (iframe.contentWindow) {
            iframe.contentWindow.postMessage({ type: 'SIDEBAR_SHOWN' }, '*');
        }
    };
    const hideSidebar = () => {
        if (!isMouseOverSidebar && !isMouseDown) {
            sidebarHtml.style.transform = `translateX(${isLeftSided ? '-100%' : '100%'})`;
            if (iframe.contentWindow) {
                iframe.contentWindow.postMessage({ type: 'SIDEBAR_HIDDEN' }, '*');
            }
        }
    };

    const onMouseMove = (e) => {
        if (!isMouseDown) return;
        const change = isLeftSided ? e.clientX - startX : startX - e.clientX;
        const newWidth = Math.max(45, Math.min(startWidth + change, window.innerWidth / 2));
        sidebarHtml.style.width = `${newWidth}px`;
    };

    const onMouseUp = () => {
        isMouseDown = false;
        document.documentElement.removeEventListener('mousemove', onMouseMove);
        document.documentElement.removeEventListener('mouseup', onMouseUp);
        resizeHandle.style.backgroundColor = 'transparent';
        sidebarHtml.style.pointerEvents = 'auto';
        document.body.style.cursor = 'default';

        const newWidth = sidebarHtml.style.width;
        localStorage.setItem('sidebarWidth', newWidth);
        sendRuntimeMessage('updateSidebarWidth', { width: newWidth });

        // Reset mouse over state after resizing
        isMouseOverSidebar = false;
        // hideSidebar();
    };

    const debouncedOnMouseMove = debounce(onMouseMove, 10);

    resizeHandle.addEventListener('mousedown', (e) => {
        e.preventDefault();
        isMouseDown = true;
        startX = e.clientX;
        startWidth = parseInt(getComputedStyle(sidebarHtml).width, 10);
        document.documentElement.addEventListener('mousemove', debouncedOnMouseMove);
        document.documentElement.addEventListener('mouseup', onMouseUp);
        resizeHandle.style.backgroundColor = 'rgba(0,0,0,0.1)';
        sidebarHtml.style.pointerEvents = 'none';
        document.body.style.cursor = 'ew-resize';
    });

    let showSidebarTimeout;

    sidebarHtml.addEventListener('mouseenter', () => {
        isMouseOverSidebar = true;
        clearTimeout(showSidebarTimeout);
        showSidebarTimeout = setTimeout(showSidebar, 500);
    });

    sidebarHtml.addEventListener('mouseleave', () => {
        isMouseOverSidebar = false;
        clearTimeout(showSidebarTimeout);
        if (!isMouseDown) hideSidebar();
    });

    const hoverThreshold = 20;
    const handleMouseMove = (e) => {
        if (isMouseDown) return;  // Don't hide sidebar while resizing
        const showCondition = isLeftSided ?
            e.clientX <= hoverThreshold :
            e.clientX >= document.body.offsetWidth - hoverThreshold;

        if (showCondition) {
            showSidebar();
        } else if (!isMouseOverSidebar) {
            hideSidebar();
        }
    };

    document.addEventListener('mousemove', handleMouseMove);

    document.body.appendChild(sidebarHtml);

    chrome.runtime.onMessage.addListener((message) => {
        if (message.action === 'updateSidebarWidth') {
            sidebarHtml.style.width = message.data.width;
        }
    });
};

export const removeSidebar = () => {
    const sidebar = document.getElementById('customSidebar');
    if (sidebar) sidebar.remove();
};

export { createSidebar };